// import axios from 'axios'
import React, { Component} from "react";
import { Redirect } from 'react-router-dom';
import Header from './Header';
import Footer from './Footer'
import Menu from "./Menu";
// import ReactPaginate from 'react-paginate';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';



 export default class ResponseTable extends Component {

    constructor(props) {
        super(props);

        this.state = {
            Response:[]
           
        };
    }

  
   

    componentDidMount() {
             if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }
          const Response = JSON.parse(localStorage.getItem('ResponseData'));
          this.setState({
            
            Response: Response.data,
        })

    }

    render() {

        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/'} />)
        }

        return (
            <div>
                <Header />
                <Menu />
                <div className="content-wrapper">
                    <br />
                    <section className="content">
                        <div className="container-fluid">

                            <div className="row">

                                <div className="col-12">

                                    <div className="card">
                                        <div className="card-header">
                                            <h3 className="card-title">Response Table</h3>
                                        </div>
                                        {/* /.card-header */}
                                        <div className="card-body">

                                            <table id="emp" class="table">
                                                <thead>
                                                    <tr>
                                                        <th>Section</th>
                                                        <th>Item</th>                                                            <br />

                                                        <th>Q1 2019</th>
                                                   

                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {this.state.Response && this.state.Response.map(category => (

                                                        <tr>
                                                            <td>
                                                                {/* {category.economic_code} */}
                                                                <b> {category?category.economic_code!=='None':""}</b>

                                                                {category.fields.map(economic => (
                                                                    <div>
                                                                <hr/>

                                                                    {economic?economic.economic_code!=='None':""}
                                                                    </div>
                                                                ))}
                                                            </td>

                                                            <td>
                                                                <b>{category.name}
                                                                </b>
                                                                <br/>
                                                                {category.fields.map(field => (
                                                          <div style={{ fontWeight: 'lighter'}}>

                                                                 <hr/>
                                                                    { field.name }
                                                                    </div>

                                                                ))}

                                                            </td>

                                                            <br />
                                                            <td>
                                                            <br />

                                                                {category.fields.map(number => (
                                                                    <div>
                                                             <hr/>

                                                                        {number.field_total_sum}
                                                                        </div>
                                                                ))}
                                                            </td>
                                                            <td>
                                                                
                                                            </td>

                                                        </tr>

                                                    ))}

                                                </tbody>

                                            </table>
                                            <div>
                                                <ReactHTMLTableToExcel
                                                    className="btn btn-info"
                                                    table="emp"
                                                    filename="ReportSurvey"
                                                    sheet="Sheet"
                                                    buttonText="Export excel" />
                                            </div>
                                            {/* */}

                                        </div>
                                    </div>
                                </div>


                            </div>


                        </div>


                    </section>
                </div>

                <Footer />
            </div>

        );
    }
}


