export default class PensiondeductionSurveyList extends Component {

    constructor(props) {
        super(props);
        this.onChangeApprove = this.onChangeApprove.bind(this);
        this.onChangeRemark = this.onChangeRemark.bind(this);

        this.state = {
            warningLoad: false,
            myloading: true,
            loading:false,
            message: "",
            Surveys: [],
            ApproveThisSurvey:[],
            approve:"",
            remark:"",
            offsetPensionDeductionSurvey: 0,
            listOfPensionDeductionSurvey:[],
            perPagePensionDeductionSurvey: 10,
            currentPagePensionDeductionSurvey: 0,  



        };
    }
    
     //pagination PensionDeductionSurvey
     handlePageClickPensionDeductionSurvey = (e) => {
        const selectedPage = e.selected;
        const offsetPensionDeductionSurvey = selectedPage * this.state.perPagePensionDeductionSurvey;
        this.setState({
             currentPagePensionDeductionSurvey: selectedPage,
             offsetPensionDeductionSurvey: offsetPensionDeductionSurvey
        }, () => {
             this.loadMoreDataPensionDeductionSurvey()
        });

   };

   //pagination PensionDeductionSurvey
   loadMoreDataPensionDeductionSurvey() {
        const data = this.state.listOfPensionDeductionSurvey;
        const slice = data.slice(this.state.offsetPensionDeductionSurvey, this.state.offsetPensionDeductionSurvey + this.state.perPagePensionDeductionSurvey)
        this.setState({
             pageCountPensionDeductionSurvey: Math.ceil(data.length / this.state.perPagePensionDeductionSurvey),
             mylistPensionDeductionSurvey: slice
        })

   }
    onChangeApprove(e) {
        this.setState({
            approve: e.target.value
        });
      }
       onChangeRemark(e) {
        this.setState({
            remark: e.target.value
        });
      }

    componentDidMount() {

        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }
        const mytoken = authService.getCurrentUser();
        const token = mytoken.token;
        // /history/survey/
        axios.get("https://surveyng.cbn.gov.ng/api/survey/api/deduction-pension/list/?status=---&close=no",
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                if (res.data) {
                    localStorage.setItem("deductionPension", JSON.stringify(res.data));
                }
                // (res);
                // (res.data);
          
                const PensionDeductiontdata = res.data;
                const slicePensionDeduction = PensionDeductiontdata.slice(this.state.offsetResLgas, this.state.offsetPensionDeductionSurvey + this.state.perPagePensionDeductionSurvey);

             this.setState({
                  listOfPensionDeductionSurvey: PensionDeductiontdata,
                  pageCountPensionDeductionSurvey: PensionDeductiontdata ? Math.ceil(PensionDeductiontdata.length / this.state.perPagePensionDeductionSurvey) : "",
                  mylistPensionDeductionSurvey: slicePensionDeduction,
                  myloading: false,
                  warningLoad: true,
                });


            })

    }
    PensiondeductionResponse(survey){

        const surveyId = survey.pk
        // console.log(survey.pk)
        const mytoken = authService.getCurrentUser();
        const token = mytoken.token;

        this.setState({
            loadingModal: true,
          });
          
        axios.get(`https://surveyng.cbn.gov.ng/api/survey/api/deduction-pension/detail/?survey_pk=${surveyId}`,
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                window.location = "/CompletePensiondeduction"

                if (res.data) {
                    localStorage.setItem("PensiondeductionInfo", JSON.stringify(res.data));
                }

                this.setState({
                  ApproveThisSurvey: res.data,
                //   loadingModal: false,
                });

              })
    
      }

    render() {

        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/'} />)
        }


        return (
            <div>
                                <Menu />

                <Header />

                <div className="content-wrapper">

                         <br />
                         {this.state.loadingModal && (
                        <span className="">
                            <div className="overlay-wrapper">
                                <div className="overlay ">         
                      <center> <img src={Logo} height="50px" alt="" /></center><br />
                             <div className="text-bold pt-2"> &nbsp;&nbsp;Please wait ...</div></div>
                            </div>

                        </span>
                     )}
                <section className="content">

                              <div className="container-fluid">

                                   <div className="row">

                                        <div className="col-12">
                                             {this.state.myloading ? (
                                                  
                                                       <center><Spinner animation="border" variant="success" /></center>
                                             ) : (

                                                  <div className="row">
                                                       <div className="col-12">
                                                            <div className="card">
                                                                 <div className="card-header">
                                                                      <h3 className="card-title">Surveys to Approve</h3>
                                                                      <div className="card-tools">
                                                                           
                                                                      </div>
                                                                 </div>
                                                                 {/* /.card-header */}
                                                                 {this.state.myloading ? (
                                                                 <center><Spinner animation="border" variant="success" /></center>
                                                                  ) : (
                                                                 <div className="card-body table-responsive p-0">
                                                                      <table className="table table-hover text-nowrap">
                                                                           <thead>
                                                                                <tr>
                                                                                <th>Category</th>
                                                                                     {/* <th>Type</th> */}
                                                                                     <th>Year</th>
                                                                                     <th>Quarter</th>
                                                                                     {/* <th>Created by</th> */}
                                                                                     <th>Action </th>
                                                                                </tr>
                                                                           </thead>
                                                                           {this.state.mylistPensionDeductionSurvey&&this.state.mylistPensionDeductionSurvey.map(survey => (

                                                                           <tbody>
                                                                                <tr>
                                                                                <td>{survey.category}</td>
                                                                                     {/* <td>{survey.survey_type}</td> */}
                                                                                     <td>{survey.year}</td>
                                                                                     <td>{survey.quarter}</td>
                                                                                     {/* <td><span className="tag tag-success">{survey.created_by}</span></td> */}
                                                                                     <td className="">
                                                                                           <button
                                                                                               className="btn btn-primary"
                                                                                               disabled={this.state.loading}
                                                                                               onClick={this.PensiondeductionResponse.bind(this, survey)}

                                                                                           >Fill 
                                                                                         </button>
                                                                                           
                                                                                         
                                                                                         </td>
                                             

                                                                                </tr>
                                                                               
                                                                           </tbody>
                                                                           ))}
                                                                           <ReactPaginate
                                                                                     previousLabel={"prev"}
                                                                                     nextLabel={"next"}
                                                                                     breakLabel={"..."}
                                                                                     breakClassName={"break-me"}
                                                                                     pageCount={this.state.pageCountPensionDeductionSurvey}
                                                                                     marginPagesDisplayed={2}
                                                                                     pageRangeDisplayed={5}
                                                                                     onPageChange={this.handlePageClickPensionDeductionSurvey}
                                                                                     containerClassName={"pagination"}
                                                                                     subContainerClassName={"pages pagination"}
                                                                                     activeClassName={"active"} />
                                                                         
                                                                      </table>
                                                                 </div>
                                                                )}
                                                                 {/* /.card-body */}
                                                            </div>
                                                            {/* /.card */}
                                                       </div>
                                                  </div>

                                             )}
                                        </div>


                                   </div>


                              </div>


                         </section>

                </div>
                <Footer />

                </div>
        );
    }
}

             