     handleRegister(e) {
          e.preventDefault();
          this.setState({
               message: "",
               successful: false,
               loading: true
          });
          this.form.validateAll();
          if (this.checkBtn.context._errors.length === 0) {
               AuthService.createNewUser(
                    this.state.username,
                    this.state.password,
                    this.state.first_name,
                    this.state.last_name,
                    this.state.address,
                    this.state.lga,
                    this.state.state,
                    this.state.dob,
                    this.state.email,
                    this.state.phone_number,

               ).then(
                    response => {
                         this.setState({
                              message: response.data.message,
                              successful: true
                         });
                    },
                    error => {
                         const resMessage =
                              (error.response &&
                                   error.response.data.detail &&
                                   error.response.data.detail) ||
                              error.message ||
                              error.toString();

                         this.setState({
                              successful: false,
                              message: resMessage
                         });
                    }
               );
          }
     }
