import React from 'react'
import TextInput from './FormElements/TextInput'
import SelectOption from './FormElements/SelectOption'

const FormElementCategory = ( { 
    field_category: { input_type, pk, name, value, }},
    ) => {
  
    switch (input_type) {
        case 'text_input_':
            return (<TextInput
                pk={pk}
                name={name}
                input_type={input_type}
                value={value}

            />)
        case 'number':
            return (<TextInput
                pk={pk}
                name={name}
                input_type={input_type}
                value={value}
            />)
        case 'text_area_':
            return (<SelectOption
                pk={pk}
                name={name}
                input_type={input_type}
                value={value}
            />)

        default:
            return null;
    }


}

export default FormElementCategory
