export default class RespondentList extends Component {

  constructor(props) {
    super(props);
   

    this.state = {
      
      Users:[],
      successful: false,
      message: "",
      loading: true,
      loadingModal:false
    };
  }


  componentWillMount(){
    if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
      return (<Redirect to={'/login'} />)
    }
     const mytoken = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
     const token = mytoken.token;
     axios.get("https://surveyng.cbn.gov.ng/api/accounts/api/list/profile/?active=yes",
     {
       headers:
       {
         'Authorization': `Token ${token}`,
         'Access-Control-Allow-Origin': '*',
         'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
         'Access-Control-Allow-Credentials': true
       },
     })
     .then(res => {
       if (res.data) {
         localStorage.setItem("Users", JSON.stringify(res.data));
       }
       this.setState({
         Users: res.data,
         loading:false
     });
       // (res);
      //  console.log(res.data);

     })

   


  }

  ApproveResponder(users){

    const userId = users.pk
    // console.log(users.pk)
    const mytoken = AuthService.getCurrentUser();
    const token = mytoken.token;

    this.setState({
        loadingModal: true,
      });
    axios.get(`https://surveyng.cbn.gov.ng/api/accounts/api/details/profile/${userId}/`,
        {
            headers:
            {
                'Authorization': `Token ${token}`,
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                'Access-Control-Allow-Credentials': true
            },
        })
        .then(res => {
            window.location = "/ApproveResponder"

            if (res.data) {
                localStorage.setItem("ReviewResponderList", JSON.stringify(res.data));
            }

            this.setState({
              ReviewResponder: res.data,
              // loadingModal: false,
            });

          })

  }

  
 
  
  render() {

    
    // console.log(list_of_lga);
    // const Users = JSON.parse(localStorage.getItem('Users'))




    return (
      <div>
        <Header />

        <div className="content-wrapper" >
        {this.state.loadingModal && (
                        <span className="">
                            <div className="overlay-wrapper">
                                <div className="overlay ">         
                      <center> <img src={Logo} height="50px" alt="" /></center><br />
                             <div className="text-bold pt-2"> &nbsp;&nbsp;Please wait ...</div></div>
                            </div>

                        </span>
                     )}
          <Menu />
          <div className="col-md-12" >
            <br />
            {this.state.loading ? (
                                <>
                                    <center><Spinner animation="border" variant="success" /></center>
                                </>
                                ) : (
          <section className="content">
            <div className="row">
              <div className="col-12">
                <div className="card">
                  <div className="card-header">
                    <h3 className="card-title">List of Respondents</h3>
                    <div className="card-tools">

                    </div>
                  </div>
                  {/* /.card-header */}

                  <div className="card-body table-responsive p-0">

                    <table className="table table-hover text-nowrap">
                      <thead>
                        <tr>
                          <th>Name</th>
                          <th>Phone</th>
                          <th>State</th>
                          <th>LGA</th>
                          <th>Registered on</th>
                          <th>designation</th>
                          <th>Branch</th>
                          <th>Action</th>
                          
                        </tr>
                      </thead>
                      {this.state.Users && this.state.Users.map(users => (

                        <tbody>
                          <tr>
                            <td>{users.username}&nbsp;</td>
                            <td>{users.phone_number}</td>
                            <td>{users.state_title}</td>
                            <td>{users.lga_title}</td>
                            <td>{users.date_joined}</td>
                            <td>{users.designation_title}</td>
                            <td>{users.branch_name}</td>
                                     <td className="">
                                          <Link 
                                          className="btn btn-lg btn-success "
                                           disabled={this.state.loading}
                                          onClick={this.ApproveResponder.bind(this, users)}

                                           >

                                               Approve
                                                   </Link>&nbsp;&nbsp;

                                                   
                                                  </td>

                          </tr>

                        </tbody>
                      ))}
                    </table>
                  </div>


                  {/* /.card-body */}
                </div>
                {/* /.card */}
              </div>
            </div>
          </section>

          )}
        </div>
                               
        </div>
        <Footer />

        </div>


    );
  }
}