export default class ViewNotification extends Component {

  constructor(props) {
    super(props);


    this.state = {
      message: "",
      loading: true,
      AdoptSurveymessage: "",
      AdoptSurveysuccessful: false,
      AdoptSurveyloading: false,
      survey: ""

    };
  }



  componentDidMount() {
    if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
      return (<Redirect to={'/login'} />)
    }
    const mytoken = authService.getCurrentUser();
    const token = mytoken.token;
    axios.get('https://surveyng.cbn.gov.ng/api/core/api/user/notification/list/',
      {
        headers:
        {
          'Content-Type': 'application/json',
          'Authorization': `Token ${token}`,
        },

      },
    )
      .then(res => {
        if (res.data) {
          localStorage.setItem("notification", JSON.stringify(res.data));
        }
        this.setState({
          notification: res.data.length,
          message: res.data,
          loading: false,

        });
        // console.log(res.data)                  

      })

  }




  render() {

    //check for user to access the page
    if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
      return (<Redirect to={'/login'} />)
    }
    const ApproveNotifications = JSON.parse(localStorage.getItem('ApproveNotifications'));
    const MyNotification = ApproveNotifications.message.slice(37, -670)
    const MyNotificationdear = ApproveNotifications.message.slice(0, -670)
    // console.log(MyNotification)
    return (
      <div>
        <Header />
        <div className="content-wrapper" style={{ minHeight: 1602 }}>
          <Menu /><br />
          <section className="content">
            <div className="row">
              <div className="col-md-3">
                <Link className="btn btn-success btn-block mb-3">Viw Notification</Link>

                <div className="card">
                  <div className="card-header">
                    <h3 className="card-title">Folders</h3>
                    <div className="card-tools">
                      <button type="button" className="btn btn-tool" data-card-widget="collapse">
                        <i className="fas fa-minus" />
                      </button>
                    </div>
                  </div>
                  <div className="card-body p-0">
                    <ul className="nav nav-pills flex-column">
                      <li className="nav-item active">
                        <Link to="/Notifications" className="nav-link">
                          <i className="fas fa-inbox" /> Inbox
                <span className="badge bg-primary float-right">{this.state.notification}</span>
                        </Link>
                      </li>
                      <li className="nav-item">
                        <Link to="#" className="nav-link">
                          <i className="far fa-envelope" /> Read
              </Link>
                      </li>

                     
                    </ul>
                  </div>
                  {/* /.card-body */}
                </div>

              </div>
              {/* /.col */}
              <div className="col-md-9">
                <div className="card card-success card-outline">
                  <div className="card-header">
                    <h3 className="card-title">Read Notification</h3>
                    <div className="card-tools">
                      <Link to="#" className="btn btn-tool" title="Previous"><i className="fas fa-chevron-left" /></Link>
                      <Link to="#" className="btn btn-tool" title="Next"><i className="fas fa-chevron-right" /></Link>
                    </div>
                  </div>
                  {/* /.card-header */}
                  <div className="card-body p-0">
                    <div className="mailbox-read-info">
                      <h5>{ApproveNotifications.subject}</h5>
                      <h6>From: support@cbn.gov.ng.
          <span className="mailbox-read-time float-right">{ApproveNotifications.created}</span></h6>
                    </div>

                    <div className="mailbox-read-message">
                      <p>{MyNotificationdear.slice(0, 37)}</p>

                      <div style={{ whiteSpace: "pre-wrap" }}>{MyNotification}</div>

                      <p>Thanks,<br />{MyNotificationdear.slice(0, 37)}</p>
                    </div>
                    {/* /.mailbox-read-message */}
                  </div>

                  
                  {/* /.card-footer */}
                </div>
                {/* /.card */}
              </div>

              {/* /.col */}
            </div>
            {/* /.row */}
          </section>

        </div>
        <Footer />
      </div>
    );
  }
}



