import React, { useContext } from 'react';
import { FormContext } from '../FormContext';



const TextInputPensionRes = ({ input_type, pk, name, field_value, value }) => {
    // console.log(field_value)
    const { handleChange } = useContext(FormContext)
    return (

        <div className="col-4">

            {name} : <label style={{ color: 'blue' }}>&nbsp;&nbsp;{value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} </label>
            <input
                onKeyDown={(evt) => ["e", "E", "+", "-"].includes(evt.key) && evt.preventDefault()}
                type="number"
                step="any"
                className="form-control"
                placeholder="number"
                min={0}
                onChange={event => handleChange(pk, event)}
                value={field_value}
            />

            <br />
        </div>




    )
}

export default TextInputPensionRes
