import React, { Component } from "react";
import { Redirect, Link } from 'react-router-dom';
import Form from "react-validation/build/form";
// import Input from "react-validation/build/input";
// import Textarea from "react-validation/build/textarea";
import CheckButton from "react-validation/build/button";
import Select from "react-validation/build/select";
import Header from './Header';
import Footer from './Footer'
import axios from "axios";
// import Spinner from 'react-bootstrap/Spinner'
import Menu from "./Menu";
import AuthService from "../services/auth.service";
// import Logo from '../cbn.png'
// import { noConflict } from "jquery";



const required = (value) => {
     if (!value) {
          return (
               <div className="alert alert-danger" role="alert">
                    This field is required!
               </div>
          );
     }
};
export default class ReviewResponder extends Component {

     constructor(props) {
          super(props);
          this.handleActivate = this.handleActivate.bind(this);
          this.onChangeActive = this.onChangeActive.bind(this);
         

          this.state = {
               warningLoad: false,
               myloading: true,
               loading: false,
               message: "",
               active:'',
               Surveys: [],
               ApproveThisSurvey: [],
               approve: "",
               remark: "",
               SurveyInfo: []
          };
     }


     onChangeActive(e) {
          this.setState({
               active: e.target.value
          });
     }
     

     componentDidMount() {
          if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
               return (<Redirect to={'/login'} />)
             }
          const script = document.createElement("script");
          script.src = "./plugins/filterizr/jquery.filterizr.min.js";
          script.async = true;
          document.body.appendChild(script);
     }

     handleActivate(e) {

          e.preventDefault();
          const ReviewResponderInfo = JSON.parse(localStorage.getItem('ReviewResponderInfo'));
          const pk =ReviewResponderInfo.pk

          this.setState({
               message: "",
               successful: true,
               loading: true
          });
          this.form.validateAll();

          if (this.checkBtn.context._errors.length === 0) {

              
               // console.log(body) 
               let formData = new FormData();
               formData.append('active', this.state.active);
              
               const mytoken = AuthService.getCurrentUser();
               const token = mytoken.token;
               axios.patch(`https://surveyng.cbn.gov.ng/api/accounts/api/update/profile/${pk}/`,formData, 
               
               

                    {
                         headers:
                         {
                              'Authorization': `Token ${token}`,
                              'Access-Control-Allow-Origin': '*',
                              'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                              'Access-Control-Allow-Credentials': true
                         },
                    }).then(

                         response => {

                              this.setState({
                                   message: "User Activated successefully",
                                   successful: true
                              });
                              window.location = "/RespondentInfo"
                                             // console.log(response.data) 

                         },
                         error => {
                              const resMessage =
                                   (error.response &&
                                        error.response.data.detail &&
                                        error.response.data) ||
                                   error.message ||
                                   error.toString();

                              this.setState({
                                   loading: false,
                                   message: resMessage,
                                   ViewTemplateloading: false,

                              });
                         }
                    );
          } else {
               this.setState({
                    loading: false
               });
          }
     }

     render() {

          if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
               return (<Redirect to={'/'} />)
          }
          const ReviewResponderInfo = JSON.parse(localStorage.getItem('ReviewResponderInfo'));

          const link='https://gfso.chinikiguard.com'

          return (

               <div>
               <Header />

               <div className="content-wrapper" >
                    <Menu />
                    <br />
                    <section className="content">
                         <div className="container-fluid">
                              <div className="row">
                                   <div className="col-md-3">
                                        {/* Profile Image */}
                                        <div className="card card-success card-outline">
                                             <div className="card-body box-profile">
                                                  
                                                  <h3 className="profile-username text-center">{ReviewResponderInfo.fullname}</h3>
                                                  <p className="text-muted text-center">{ReviewResponderInfo.staff_number}</p>
                                                  <p className="text-muted text-center">{ReviewResponderInfo.active}</p>

                                                  <div className="card-body"><strong><i className="fas fa-book mr-1" /> Designation</strong>
                                                  <p className="text-muted">{ReviewResponderInfo.designation_title}</p><hr />
                                                  {/* <strong>
                                                  <i className="fas fa-home mr-1" /> Branch</strong>
                                                  <p className="text-muted">{ReviewResponderInfo.branch_name}</p>
                                                   */}
                                                  <hr /><strong><i className="fas fa-map-marker-alt mr-1" /> State</strong>
                                                  <p className="text-muted">{ReviewResponderInfo.state_title}</p><hr />
                                                       <strong><i className="fas fa-map-marker-alt mr-1" /> LGA</strong>
                                                       <p className="text-muted">{ReviewResponderInfo.lga_title}</p>
                                                  </div>
                                                  <Form
                                                       onSubmit={this.handleActivate}
                                                       ref={c => {
                                                            this.form = c;
                                                       }}
                                                  >
                                                       
                                                       <div className="card-body">
                                                       
                                                            <div className="form-group">
                                                                 <label htmlFor="exampleFormControlSelect9">Change Status</label>
                                                                 <Select
                                                                      className="form-control"
                                                                      id="exampleFormControlSelect9"
                                                                      validations={[required]}
                                                                      onChange={this.onChangeActive}
                                                                      value={this.state.active}
                                                                 >
                                                                      <option>--select--</option>
                                                                      <option value="yes" className="text-success">Activate</option>
                                                                      <option value="no" className="text-danger">DeActivate</option>

                                                                 </Select>
                                                            </div>
                                                            <div className="form-group">
                                                                 <button
                                                                      className="btn btn-primary btn-sm"
                                                                      disabled={this.state.loading}
                                                                 >
                                                                      {/* {this.state.loading && (
                                            <span className="spinner-border spinner-border-sm"></span>
                                        )} */}
                                                                      <span>Submit</span>
                                                                 </button>
                                                            </div>
                                                            <center>
                                              {this.state.message && (
                                                <div className="form-group">
                                                  <div
                                                    className={
                                                      this.state.successful
                                                        ? "alert alert-success"
                                                        : "alert alert-danger"
                                                    }
                                                    role="alert"
                                                  >
                                                    {this.state.message}
                                                  </div>
                                                </div>
                                              )}

                                            </center>
                                                            <CheckButton
                                                                 style={{ display: "none" }}
                                                                 ref={c => {
                                                                      this.checkBtn = c;
                                                                 }}
                                                            />

                                                       </div>
                                                       
                                                  </Form>
                                          
                                             </div>
                                             {/* /.card-body */}
                                        </div>
                                        {/* /.card */}
                                       
                                   </div>

                                   <div className="col-md-9">
                                        <div className="card">

                                             <div className="card-header p-2">
                                                  <Form>
                                                  <ul className="nav nav-pills">
                                                  <li className="nav-item"><Link className="nav-link  " onChange={this.onChangeActive} value={this.state.active} data-toggle="tab">document</Link></li>
                                                       {/* <li className="nav-item"><Link className="nav-link" href="#timeline" data-toggle="tab">Timeline</Link></li>
                                                       <li className="nav-item"><Link className="nav-link" href="#settings" data-toggle="tab">Settings</Link></li>
                                                  */}
                                                  </ul>
                                                  </Form>
                                             </div>{/* /.card-header */}
                                             
                                             {ReviewResponderInfo.get_registration_documents.length?

                                             <div className="card-body">
                                                  <div className="tab-content">
                                                       <div className="active tab-pane" id="activity">
                                                       
                                                                                     <div className="card-footer bg-white">
                                                                                          <ul className="mailbox-attachments d-flex align-items-stretch clearfix">
                                                                                          {ReviewResponderInfo.get_registration_documents.map(document => (

                                                                                               <li>
                                                                                                    <span className="mailbox-attachment-icon"><i className="far fa-file-pdf" /></span>
                                                                                                    <div className="mailbox-attachment-info">
                                                                                                         <a href={link+document.document} className="mailbox-attachment-name" download><i className="fas fa-paperclip" /> {document.info}</a>
                                                                                                         <span className="mailbox-attachment-size clearfix mt-1">
                                                                                                              <span>{document.title}</span>
                                                                                                              <a href={link+document.document}className="btn btn-default btn-sm float-right" download={link+document.document}><i className="fas fa-cloud-download-alt" /></a>
                                                                                                         </span>
                                                                                                    </div>
                                                                                               </li>
                                                                                                                                                       ))}

                                                                                               
                                                                                               
                                                                                          </ul>
                                                                                     </div>

                                                            {/* /.post */}
                                                            {/* Post */}
                                                            <div className="post clearfix">
                                                               
                                                                 {/* /.user-block */}
                                                               
                                                            </div>
                                                            {/* /.post */}

                                                       </div>
                                                      
                                                  </div>
                                                  {/* /.tab-content */}
                                             </div>
 : <center> 
 <h6>Sorry there is no document attached by this user</h6></center>
}
                                             
                                        </div>
                                   
                                        {/* /.card */}
                                   </div>
                                   
                                    </div>
                         
                              {/* /.row */}
                         </div>{/* /.container-fluid */}
                    </section>


               </div>
               <Footer />

          </div>


                );
     }
}

