import React, { Component } from "react";
import { Redirect } from 'react-router-dom';
import axios from 'axios'
import authService from '../services/auth.service';


const required = value => {
    if (!value) {
        return (
            <div className="alert alert-danger" role="alert">
                This field is required!
            </div>
        );
    }
};

export default class CheckSurveyTypeResponse extends Component {

    constructor(props) {
        super(props);
        this.onChangeState = this.onChangeState.bind(this);
        this.onChangeLga = this.onChangeLga.bind(this);

        this.state = {
            statee:"",
            lga:"",
            myloading: false,
            statesGov: false,
            lgasGov: false,
            othersGov: false,
            user_type:false
        };
    }

    onChangeState(e) {
        this.setState({
            statee: e.target.value
        });
    }
    onChangeLga(e) {
        this.setState({
            lga: e.target.value
        });
    }

    componentDidMount() {
        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }
        

        const state = "Ahmad loophole";
        const user = authService.getCurrentUser();

        const token = user.token;
        axios.get(`https://surveyng.cbn.gov.ng/api/locations/api/list/state/`,
            {
                headers:
                {
                    'Content-Type': 'application/json',
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
                params:
                {
                    state: state
                }
            },
        )
            .then(res => {
                // if (res.data) {
                //     localStorage.setItem("List_Of_State", JSON.stringify(res.data));
                // }
                this.setState({
                    List_Of_State:res.data
                });
                // console.log(res.data)                  

            })
        axios.get(`https://surveyng.cbn.gov.ng/api/locations/api/list/lga/`,
            {
                headers:
                {
                    'Content-Type': 'application/json',
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
                params:
                {
                    state: state
                }
            },
        )
            .then(res => {
                // if (res.data) {
                //     localStorage.setItem("List_Of_Lga", JSON.stringify(res.data));
                // }
                this.setState({
                    List_Of_Lga:res.data
                });

            });
         // console.log(res.data)   

        // check for the value selected and show the related content
        try {
            const TargetResponse = JSON.parse(localStorage.getItem('Surveydetails'));
            const TargetUser = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));

            const check = TargetResponse.survey_type
            const user_type = TargetUser.profile.user_type


            
            if (check) {
                this.setState({
                    user_type:user_type==="hq_user",
                    statesGov: check.includes("sg"),
                    lgasGov: check.includes("lg"),
                    othersGov: check.includes("other"),
                });
            }

        } catch (e) {

            console.log(e);
        }

        localStorage.removeItem("state_pk");
        localStorage.removeItem("lga_pk");

    }
    componentDidUpdate() {
        const state_pk = this.state.statee
        const lga_pk = this.state.lga
       
        if (state_pk) {

        localStorage.setItem("state_pk",JSON.stringify(state_pk));

        }else if(lga_pk){

            localStorage.setItem("lga_pk",JSON.stringify(lga_pk));

        }else{
            return null;
        }    
    }


    render() {

         //get the list of state and LGAS  in the localStorage
        //  const List_Of_States = JSON.parse(localStorage.getItem('List_Of_State'));
        //  const List_Of_lgas = JSON.parse(localStorage.getItem('List_Of_Lga'));
         
        //check for user to access the page
        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/'} />)
        }

        //for checking the right selection either SG or LG
        const { statesGov, lgasGov, othersGov,user_type } = this.state;

        return (
            <div>

            {user_type && (
            <div>
                {statesGov && (
                    <div className="col-sm-12">
                        <div className="form-group">
                            <hr/>
                           <center> <label> Which state are you from</label></center>
                            <select type="text" className="form-control " placeholder
                               onChange={this.onChangeState}
                                required
                                value={this.state.statee}

                            >
                                <option value="">Select your State</option>
                                {this.state.List_Of_State && this.state.List_Of_State.map(state => (
                                 <option value= {state.pk}>{state.title}</option>

                             ))}
                            </select>
                        </div>
                    </div>

                )}
                {lgasGov && (
                    <div className="col-sm-12">
                        <div className="form-group">
                        <center> <label> Which LGA are you from</label></center>
                            <select type="text" className="form-control " placeholder
                                onChange={this.onChangeLga}
                                validations={[required]}
                                value={this.state.lga}

                            >
                                <option value="">Select your LGA</option>
                                {this.state.List_Of_Lga && this.state.List_Of_Lga.map(lga => (
                                 <option value= {lga.pk}>{lga.title}</option>

                             ))}
                            </select>
                        </div>
                    </div>
                )}
                {othersGov && (
                    <div className="col-sm-6">
                        <div className="form-group">
                            <label> Survey Type</label>
                            <select type="text" className="form-control " placeholder 
                                validations={[required]}
                            >
                                <option value="">Select</option>
                                <option value="SG">SG</option>
                                <option value="LG">LG</option>
                                <option value="OTHER">OTHER</option>
                            </select>
                        </div>
                    </div>
                )}
            </div>
             
             ) }
            </div>
            );   
                }
}