import axios from "axios";


const Url ='https://surveyng.cbn.gov.ng/api/accounts/api/jwt/';
class AuthService {

 
  login(username, password) {
    

    return axios.post( Url, { username,  password },  { headers: { 
    'Access-Control-Allow-Origin': '*',
    'Access-Control-Allow-Methods': 'GET,HEAD,OPTIONS',
    'Access-Control-Allow-Credentials': true }, }   )
    
      .then(response => {
        if (response.data) {
          localStorage.setItem("_WannanGfsoCbnUserNe", JSON.stringify(response.data));
        }
        // console.log(response.data)
        return response.data;
      }
      
      );
    
  }

 //handle logout
  // logout() {
  //   localStorage.removeItem("user");
  //   localStorage.removeItem("token");
  // }
//create new user
  createNewUser( username,password,first_name,last_name,
    // address, 
    lga,state,
    // dob,
    email,phone_number,group_pk,
    // branch_pk,
    designation_pk,
    // staff_number,
    user_type) {
//     const mytoken = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
// const token = mytoken.token;

let formData = new FormData();
        formData.append('username', username);
        formData.append('password', password);
        formData.append('first_name', first_name);
        formData.append('last_name', last_name);
        // formData.append('address', address);
        formData.append('lga', lga);
        // formData.append('dob', dob);
        formData.append('state', state);
        formData.append('email', email);
        formData.append('phone_number', phone_number);
        formData.append('group_pk', group_pk);
        formData.append('designation_pk', designation_pk);
        // formData.append('branch_pk', branch_pk);
        // formData.append('staff_number', staff_number);
        formData.append('user_type', user_type);
        
        

    return axios.post('https://surveyng.cbn.gov.ng/api/accounts/api/create-user/',formData, {
     
    },
    {  
      headers: { 
      'Content-Type':'application/json',
       'Access-Control-Allow-Origin': '*',
       'Access-Control-Allow-Methods': 'POST, GET,HEAD,OPTIONS',
       'Access-Control-Allow-Credentials': true 
      }, 
    }   
    )
  }

  //this handle projects
  RegisterProject(title,description,location,status,start_date,due_date){
    const mytoken = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
const token = mytoken.token;
     return  axios.post("https://ecological.chinikiguard.com/projects/api/create/", {

     title,
     description,
     location,
     status,
     start_date,
     due_date,
  },
  
  { 
    
    
    headers: { 
    'Authorization': `Token ${token}`,
     'Access-Control-Allow-Origin': '*',
     'Access-Control-Allow-Methods': 'GET,HEAD,OPTIONS',
     'Access-Control-Allow-Credentials': true 
    }, 
  }   
  )


  }



//this handle survey creation
CreateSurvey(title,category,status,origin){

  const mytoken = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
  const token = mytoken.token;
  return  axios.post("https://surveyng.cbn.gov.ng/api/survey/api/create/survey/", {

  title,
  category,
  status,
  origin,
},

{ 
 
 
 headers: { 
 'Authorization': `Token ${token}`,
  'Access-Control-Allow-Origin': '*',
  'Access-Control-Allow-Methods': 'GET,HEAD,OPTIONS',
  'Access-Control-Allow-Credentials': true 
 }, 
}   
)

.then(response => {
  if (response.data) {
    localStorage.setItem("survey_pk", JSON.stringify(response.data.pk));
  }
  // console.log(response.data)
  return response.data;
}

);




}

  
  getCurrentUser() {
    return JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
  }
}


export default new AuthService();
