import React, { Component } from "react";
import { Redirect, Link } from 'react-router-dom';
import Header from './Header';
import Footer from './Footer'
import axios from "axios";
import Spinner from 'react-bootstrap/Spinner'
import Menu from "./Menu";
import authService from "../services/auth.service";


export default class ReviewRespondent extends Component {

    constructor(props) {
        super(props);
        this.state = {
            warningLoad: false,
            myloading: true,
            message: "",
            Surveys: [],
        };
    }
    componentDidMount() {
        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }
        const mytoken = authService.getCurrentUser();
        const token = mytoken.token;
        axios.get("https://surveyng.cbn.gov.ng/api/survey/api/list/survey/",
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                if (res.data) {
                    localStorage.setItem("AllSurveys", JSON.stringify(res.data));
                }
                // (res);
                // (res.data);
                this.setState({
                    Surveys: res.data,
                    myloading: false,
                    warningLoad: true,
                });
            })

    }

    EditTemplate(Surveys) {

        //
        localStorage.setItem("SingleSurvey", JSON.stringify(Surveys));
        const mytoken = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
        const token = mytoken.token;
        const singleSurveyPk = Surveys.pk;
        console.log(Surveys.pk)

        // const body =
        // {
        // survey: SurveyList.pk,
        // }

        axios.get(`https://surveyng.cbn.gov.ng/api/survey/api/details/template/${singleSurveyPk}/`,
            {
                headers:
                {
                    'Content-Type': 'application/json',
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },

            })
            .then(res => {
                if (res.data) {
                    localStorage.setItem("Surveydetails", JSON.stringify(res.data));
                }
                console.log(res.data)
                // window.location = "/CompleteSurvey"

            })


    }

    render() {

        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/'} />)
        }


        return (
            <div>
                <Header />
                <Menu />
                <div className="content-wrapper">
                         <br />
                <section className="content">
                              <div className="container-fluid">

                                   <div className="row">

                                        <div className="col-12">
                                             {this.state.myloading ? (
                                                  <>
                                                       <center><Spinner animation="border" variant="success" /></center>
                                                  </>
                                             ) : (

                                                  <div className="row">
                                                       <div className="col-12">
                                                            <div className="card">
                                                                 <div className="card-header">
                                                                      <h3 className="card-title">List of Respondent</h3>
                                                                      <div className="card-tools">
                                                                           
                                                                      </div>
                                                                 </div>
                                                                 {/* /.card-header */}
                                                                 <div className="card-body table-responsive p-0">
                                                                      <table className="table table-hover text-nowrap">
                                                                           <thead>
                                                                                <tr>
                                                                                     <th>ID</th>
                                                                                     <th>User</th>
                                                                                     <th>Date</th>
                                                                                     <th>Status</th>
                                                                                     <th>Reason</th>
                                                                                </tr>
                                                                           </thead>
                                                                           <tbody>
                                                                                <tr>
                                                                                     <td>183</td>
                                                                                     <td>John Doe</td>
                                                                                     <td>11-7-2014</td>
                                                                                     <td><span className="tag tag-success">Approved</span></td>
                                                                        <td className="">
                                                                            <Link className="btn btn-lg btn-success " to="/">

                                                                                Approve
                                                   </Link>&nbsp;&nbsp;

                                                    <Link className="btn btn-lg btn-danger " to="/">
                                                                                Delete
                                                    </Link>&nbsp;&nbsp;

                                                  </td>
                                             

                                                                                </tr>
                                                                               
                                                                           </tbody>
                                                                      </table>
                                                                 </div>
                                                                 {/* /.card-body */}
                                                            </div>
                                                            {/* /.card */}
                                                       </div>
                                                  </div>

                                             )}
                                        </div>


                                   </div>


                              </div>


                         </section>

                </div>
                <Footer />

                </div>
        );
    }
}

             