// };
export default class RejectedSurvey extends Component {

    constructor(props) {
        super(props);
        this.onChangeApprove = this.onChangeApprove.bind(this);
        this.onChangeRemark = this.onChangeRemark.bind(this);
        this.onChangeTitle = this.onChangeTitle.bind(this);
        this.onChangeQuarter = this.onChangeQuarter.bind(this);
        this.onChangeStatus = this.onChangeStatus.bind(this);
        this.onChangeSurveyType = this.onChangeSurveyType.bind(this);
        this.onChangeStartDate = this.onChangeStartDate.bind(this);
        this.onChangeDueDate = this.onChangeDueDate.bind(this);
        this.onChangeYear = this.onChangeYear.bind(this);
        this.onChangeState = this.onChangeState.bind(this);
        this.onChangeLgas = this.onChangeLgas.bind(this);
        this.UpdateSurvey = this.UpdateSurvey.bind(this);
        this.handleUpdate = this.handleUpdate.bind(this);
        
        this.state = {
            warningLoad: false,
            myloading: true,
            loading: false,  
            message: "",
            Surveys: [],
            ApproveThisSurvey: [],
            approve: "",
            remark: "",
            title: "",
            quarter: "",
            status: "",
            year: "",
            survey_type: "",
            start_date: "",
            due_date: "",
            states: "",
            lgas: "",
            statesGov: false,
            lgasGov: false,
            othersGov: false,
        };
    }


    onChangeTitle(e) {
        this.setState({
            title: e.target.value
        });
    }

    onChangeQuarter(e) {
        this.setState({
            quarter: e.target.value
        });
    }

    onChangeYear(e) {
        this.setState({
            year: e.target.value
        });
    }
    onChangeStartDate(e) {
        this.setState({
            start_date: e.target.value
        });
    }
    onChangeDueDate(e) {
        this.setState({
            due_date: e.target.value
        });
    }
    onChangeSurveyType(e) {
        this.setState({
            survey_type: e.target.value
        });
    }

    onChangeStatus(e) {
        this.setState({
            status: e.target.value
        });
    }

    onChangeState(e) {
        this.setState({
            states: e.target.value
        });
    }
    onChangeLgas(e) {
        this.setState({
            lgas: e.target.value
        });
    }


    onChangeApprove(e) {
        this.setState({
            approve: e.target.value
        });
    }
    onChangeRemark(e) {
        this.setState({
            remark: e.target.value
        });
    }
    componentDidMount() {
        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
        }
        this.Get_State_Data();
        this.Get_LGA_Data();

        const mytoken = authService.getCurrentUser();
        const token = mytoken.token;
        axios.get("https://surveyng.cbn.gov.ng/api/survey/api/list/survey/?rejected=yes&deployed=no",
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                if (res.data) {
                    localStorage.setItem("RejectedSurvey", JSON.stringify(res.data));
                }
                // (res);
                // (res.data);
                this.setState({
                    Surveys: res.data,
                    myloading: false,
                    warningLoad: true,
                });
            })

    }
    componentDidUpdate(prevProps, prevState) {
        //reset to default state
        const Checked_State = [1, 1, 1]
        localStorage.setItem("Checked_State", Checked_State);
        const Checked_LGA = [1, 1, 1]
        localStorage.setItem("Checked_LGA", Checked_LGA);
     
        // check for the value selected and show the related content
        try {
            if (prevState.survey_type !== this.state.survey_type) {
                const state = this.state.survey_type
                if (state) {
                    this.setState({
                        statesGov: state.includes("sg"),
                        lgasGov: state.includes("lg"),
                        othersGov: state.includes("other"),
                    });
                }
            }

        } catch (e) {

            // console.log(e);
        }
    }

    handleUpdate(e) {

        e.preventDefault();
        // const SurveyId = JSON.parse(localStorage.getItem('Survey_Update_ID'))
    
        this.setState({
          message: "",
          successful: false,
          loading: true
        });
        this.form.validateAll();
    
        if (this.checkBtn.context._errors.length === 0) {
          
          const ApprovSurveyInfo = JSON.parse(localStorage.getItem('ApprovSurveyInfo'))
          


            //convert the string to number from the [go] array i save to  localStorage for State
            const stop_here_for_state = localStorage.getItem('Checked_State');
            const jasus_for_state = stop_here_for_state

            //convert the string to number from the [go] array i save to localStorage for LGA
            const stop_here_for_lga = localStorage.getItem('Checked_LGA');
            const jasus_for_lga = stop_here_for_lga

            //set the states user selected
            const states =
                jasus_for_state.match(/\d+/g).map(Number)

            // set the LGAs user selected
            const lgas =
                jasus_for_lga.match(/\d+/g).map(Number)




            // newly created json
            const body =
            {
                survey_pk:ApprovSurveyInfo.pk,
                title: this.state.title||ApprovSurveyInfo.title,
                quarter: this.state.quarter||ApprovSurveyInfo.quarter,
                year: this.state.year||ApprovSurveyInfo.year,
                survey_type: this.state.survey_type||ApprovSurveyInfo.survey_type,
                start_date: this.state.start_date||ApprovSurveyInfo.start_date,
                due_date: this.state.due_date||ApprovSurveyInfo.due_date,
                rejected: 'false',
                // survey_type:this.state.survey_type||ApprovSurveyInfo.survey_type,
                states_pk: states ||ApprovSurveyInfo.states,
                lgas_pk:lgas||ApprovSurveyInfo.lgas,
            }

            console.log(body);
          const mytoken = authService.getCurrentUser();
          const token = mytoken.token;
    
          axios.post(`https://surveyng.cbn.gov.ng/api/survey/api/update/survey/respondents/`, body,
          {
            headers:
            {
                'Authorization': `Token ${token}`,
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                'Access-Control-Allow-Credentials': true
            },
        },
          
          ).then(
    
            response => {
    
              this.setState({
                message: "Survey Updated Successefully",
                successful: true
              });
              window.location = "/RejectedSurvey"
            },
            error => {
              const resMessage =
                (error.response &&
                  error.response.data &&
                  error.response.data.message) ||
                error.message ||
                error.toString();
    
              this.setState({
                loading: false,
                message: resMessage
              });
            }
          );
        } else {
          this.setState({
            // loading: false
          });
        }
    
      }

    UpdateSurvey(survey) {

        const surveyId = survey.pk
        // console.log(survey.pk)
        localStorage.setItem("Survey_Update_ID", JSON.stringify(survey.pk));
        const mytoken = authService.getCurrentUser();
        const token = mytoken.token;

        this.setState({
            loadingModal: true,
        });
        axios.get(`https://surveyng.cbn.gov.ng/api/survey/api/details/${surveyId}/`,
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                window.location = "/SurveyToUpdate"

                if (res.data) {
                    localStorage.setItem("SurveyToUpdateInfo", JSON.stringify(res.data));
                }

                this.setState({
                    ApproveThisSurvey: res.data,
                    // loadingModal: false,
                });

            })

    }
    Get_LGA_Data() {
        const state = "Ahmad loophole";
        const mytoken = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
        const token = mytoken.token;
        axios.get(`https://surveyng.cbn.gov.ng/api/locations/api/list/lga/`,
            {
                headers:
                {
                    'Content-Type': 'application/json',
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
                params:
                {
                    state: state
                }
            },
        )
            .then(res => {
                if (res.data) {
                    localStorage.setItem("List_Of_Lga", JSON.stringify(res.data));
                }
                
            });
        // console.log(res.data)   
    }
    Get_State_Data() {
        const state = "Ahmad loophole";
        const mytoken = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
        const token = mytoken.token;
        axios.get(`https://surveyng.cbn.gov.ng/api/locations/api/list/state/`,
            {
                headers:
                {
                    'Content-Type': 'application/json',
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
                params:
                {
                    state: state
                }
            },
        )
            .then(res => {
                if (res.data) {
                    localStorage.setItem("List_Of_State", JSON.stringify(res.data));
                }
                
            });
        // console.log(res.data)   
    }
    render() {

        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/'} />)
        }
      
        return (
            <div>
                <Header />
                <Menu />
                <div className="content-wrapper">
                    <br />
                    {this.state.loadingModal && (
                        <span className="">
                            <div className="overlay-wrapper">
                                <div className="overlay ">
                                    <center> <img src={Logo} height="50px" alt="" /></center><br />
                                    <div className="text-bold pt-2"> &nbsp;&nbsp;Please wait ...</div></div>
                            </div>

                        </span>
                    )}
                    <section className="content">
                        <div className="container-fluid">

                            <div className="row">

                                <div className="col-12">
                                    {this.state.myloading ? (
                                        <>
                                            <center><Spinner animation="border" variant="success" /></center>
                                        </>
                                    ) : (

                                        <div className="row">
                                            <div className="col-12">
                                                <div className="card">
                                                    <div className="card-header">
                                                        <h3 className="card-title">Update Surveys</h3>
                                                        <div className="card-tools">

                                                        </div>
                                                    </div>
                                                    {/* /.card-header */}
                                                    {this.state.myloading ? (
                                                        <center><Spinner animation="border" variant="success" /></center>
                                                    ) : (
                                                        <div className="card-body table-responsive p-0">
                                                            <table className="table table-hover text-nowrap">
                                                                <thead>
                                                                    <tr>
                                                                        <th>Title</th>
                                                                        <th>Type</th>
                                                                        <th>Year</th>
                                                                        <th>Quarter</th>
                                                                        <th>Created by</th>
                                                                        <th>Remark </th>
                                                                        <th>Action </th>
                                                                    </tr>
                                                                </thead>
                                                                {this.state.Surveys.map(survey => (

                                                                    <tbody>
                                                                        <tr>
                                                                            <td>{survey.title}</td>
                                                                            <td>{survey.survey_type}</td>
                                                                            <td>{survey.year}</td>
                                                                            <td>{survey.quarter}</td>
                                                                            <td><span className="tag tag-success">{survey.created_by}</span></td>
                                                                            <td>{survey.rejection_remark}</td>

                                                                            
                                                                            <td className="">
                                                                                <button
                                                                                    className="btn btn-info btn-lg"
                                                                                    disabled={this.state.loading}
                                                                                    
                                                                                   onClick={this.UpdateSurvey.bind(this, survey)}

                                                                                >Update
                                                                               </button>
                                                                                
                                                                            </td>


                                                                        </tr>

                                                                    </tbody>
                                                                ))}
                                                            </table>
                                                        </div>
                                                    )}
                                                    {/* /.card-body */}
                                                </div>
                                                {/* /.card */}
                                            </div>
                                        </div>

                                    )}
                                </div>


                            </div>


                        </div>


                    </section>

                </div>
                <Footer />

            </div>
        );
    }
}

