import React, { Component } from "react";
import { Redirect,Link } from 'react-router-dom';
import Form from "react-validation/build/form";
// import Input from "react-validation/build/input";
import Textarea from "react-validation/build/textarea";
import CheckButton from "react-validation/build/button";
import Select from "react-validation/build/select";
import Header from './Header';
import Footer from './Footer'
import axios from "axios";
// import Spinner from 'react-bootstrap/Spinner'
import Menu from "./Menu";
import AuthService from "../services/auth.service";
// import Logo from '../cbn.png'



const required = (value) => {
     if (!value) {
          return (
               <div className="alert alert-danger" role="alert">
                    This field is required!
               </div>
          );
     }
};
export default class ApprovePensionSurvey extends Component {

     constructor(props) {
          super(props);
          this.handleApprove = this.handleApprove.bind(this);
          this.onChangeApprove = this.onChangeApprove.bind(this);
          this.onChangeRemark = this.onChangeRemark.bind(this);


          this.state = {
               successful: false,
               warningLoad: false,
               myloading: true,
               loading: false,
               message: "",
               Surveys: [],
               ApproveThisSurvey: [],
               approve: "",
               remark: "",
               PensionSurveyInfo: []
          };
     }


     onChangeApprove(e) {
          this.setState({
               status: e.target.value
          });
     }
     onChangeRemark(e) {
          this.setState({
               remark: e.target.value
          });
     }

     componentDidMount() {
          if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
               return (<Redirect to={'/login'} />)
          }

     }

     handleApprove(e) {

          e.preventDefault();
          const PensionSurveyInfo = JSON.parse(localStorage.getItem('PensionSurveyInfo'));


          this.setState({
               message: "",
               successful: false,
               loading: true
          });
          this.form.validateAll();

          if (this.checkBtn.context._errors.length === 0) {


               // console.log(body) 
               // let formData = new FormData();
               // formData.append('status', this.state.status);
               // formData.append('survey_pk', PensionPensionSurveyInfo.pk);
               // formData.append('remark', this.state.remark);

               const body = {
                    survey_pk: PensionSurveyInfo.survey_pk,
                    status: this.state.status,
                    remark: this.state.remark
               }

               const mytoken = AuthService.getCurrentUser();
               const token = mytoken.token;
               axios.post(`https://surveyng.cbn.gov.ng/api/survey/api/deduction-pension/cycle/`, body,



                    {
                         headers:
                         {
                              'Authorization': `Token ${token}`,
                              'Access-Control-Allow-Origin': '*',
                              'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                              'Access-Control-Allow-Credentials': true
                         },
                    }).then(

                         response => {

                              this.setState({
                                   message: response.data.detail,
                                   successful: true
                              });

                              // window.location = "/ListToApprovePension"
                              // console.log(response.data) 

                         },
                         error => {
                              const resMessage =
                                   (error.response &&
                                        error.response.data.detail &&
                                        error.response.data) ||
                                   error.message ||
                                   error.toString();

                              this.setState({
                                   loading: false,
                                   message: resMessage,
                                   ViewTemplateloading: false,

                              });
                         }
                    );
          } else {
               this.setState({
                    loading: false
               });
          }
     }

     render() {

          if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
               return (<Redirect to={'/'} />)
          }
          const PensionSurveyInfo = JSON.parse(localStorage.getItem('PensionSurveyInfo'));



          return (
               <div>
                    <Header />
                    <Menu />
                    <div className="content-wrapper">
                         <br />
                         {/* {this.state.loading && (
                        <span className="">
                            <div className="overlay-wrapper">
                                <div className="overlay ">         
                      <center> <img src={Logo} height="50px" alt="" /></center><br />
                             <div className="text-bold pt-2"> &nbsp;&nbsp;Please wait ...</div></div>
                            </div>

                        </span>
                     )} */}
                         <section className="content">
                              <div className="container-fluid">
                                   <div className="row">
                                        <div className="col-12">
                                             <div className="card">
                                                  <div className="card-header">
                                                       <h3 className="card-title">Surveys to Approve</h3>
                                                       <div className="card-tools">
                                                       </div>
                                                  </div>
                                                  <div className="card-body">
                                                       {/* /.card-header */}
                                                       <div className="row">
                                                            <div className="col-sm-12">

                                                                 <label> Title</label>:&nbsp;
                                                                      <center>
                                                                      {PensionSurveyInfo.category.toUpperCase()}

                                                                 </center>
                                                                 <hr />
                                                            </div>

                                                            <div className="col-sm-12">

                                                                 <label> Quarter</label>:&nbsp;
                                                                      <center>
                                                                      {PensionSurveyInfo.quarter.toUpperCase()}
                                                                 </center>
                                                                 <hr />
                                                            </div>
                                                       </div>
                                                       <div className="row">
                                                            <div className="col-sm-12">

                                                                 {/* text input */}
                                                                 <label> Year</label>:&nbsp;
                                                                      <center>
                                                                      {PensionSurveyInfo.year}
                                                                 </center>
                                                                 <hr />

                                                            </div>
                                                            {/* <div className="col-sm-12">
                                                                 
                                                                      <label> Start date</label>:&nbsp;
                                                                      <center>
                                                                                       {PensionSurveyInfo.start_date}
                                                                 </center>
                                                                 <hr />

                                                            </div>
                                                            <div className="col-sm-12">
                                                                
                                                                      <label> Due Date</label>:&nbsp;
                                                                      <center>
                                                                                         {PensionSurveyInfo.due_date}

                                                                 </center>
                                                                 <hr />

                                                            </div> */}
                                                            <div className="col-sm-12">

                                                                 <label> STATE</label>:&nbsp;
                                                                <center>
                                                                      ALL

                                                           </center>
                                                                 <hr />

                                                            </div>

                                                            {/*  */}
                                                       </div>
                                                  </div>


                                                  <Form
                                                       onSubmit={this.handleApprove}
                                                       ref={c => {
                                                            this.form = c;
                                                       }}
                                                  >
                                                       {!this.state.successful && (


                                                            <div>

                                                                 <div className="card-body">

                                                                      <div className="form-group">
                                                                           <label htmlFor="exampleFormControlSelect9">Action</label>
                                                                           <Select
                                                                                className="form-control"
                                                                                id="exampleFormControlSelect9"
                                                                                validations={[required]}
                                                                                onChange={this.onChangeApprove}
                                                                                value={this.state.status}
                                                                           >
                                                                                <option>--select--</option>
                                                                                <option value="approved" className="text-success">Approve</option>
                                                                                <option value="rejected" className="text-danger">Reject</option>

                                                                           </Select>
                                                                      </div>

                                                                      <div className="form-group">
                                                                           <Textarea type="remark" name="remark" className="form-control" placeholder="Remark" value={this.state.remark}
                                                                                onChange={this.onChangeRemark}
                                                                                validations={[required]} />

                                                                      </div>
                                                                      <br />


                                                                      <div className="form-group">
                                                                           <button
                                                                                className="btn btn-primary btn-lg"
                                                                                disabled={this.state.loading}
                                                                           >
                                                                                {/* {this.state.loading && (
                                            <span className="spinner-border spinner-border-sm"></span>
                                        )} */}
                                                                                <span>Submit</span>
                                                                           </button>
                                                                      </div>
                                                                 </div>
                                                            </div>
                                                       )}
                                                       


                                                       <CheckButton
                                                            style={{ display: "none" }}
                                                            ref={c => {
                                                                 this.checkBtn = c;
                                                            }}
                                                       />


                                                  </Form>
                                                 
                                                  {/* /.card-body */}
                                                  <center>
                                                                 {this.state.message && (
                                                                      <div className="form-group">
                                                                           <div
                                                                                className={
                                                                                     this.state.successful
                                                                                          ? "alert alert-success"
                                                                                          : "alert alert-danger"
                                                                                }
                                                                                role="alert"
                                                                           >
                                                                                {this.state.message}
                                                                           </div>
                                                                           {this.state.successful &&
                                                                                     <Link
                                                                                          className="btn btn-primary"
                                                                                          to="/ListToApprovePension"

                                                                                     >
                                                                                          continue
                                                                                              </Link>
                                                                                }


                                                                      </div>
                                                                 )}

                                                            </center>
                                             </div>
                                             {/* /.card */}
                                        </div>
                                   </div>



                              </div>


                         </section>

                    </div>
                    <Footer />

               </div>
          );
     }
}

