// import axios from 'axios'
import React, { Component} from "react";
import { Redirect } from 'react-router-dom';
import Header from './Header';
import Footer from './Footer'
import Menu from "./Menu";
// import ReactPaginate from 'react-paginate';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';



 export default class LGEONSPWReport extends Component {

    constructor(props) {
        super(props);

        this.state = {
          LGEONSPWReport:[],
          myloading: true,
         

           
        };
    }

  
   

    componentDidMount() {
             if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }
          const Response = JSON.parse(localStorage.getItem('LGEONSPWReport'));
          this.setState({
            
               LGEONSPWReport: Response.data,
               year:Response.year,
          })

                 
    }

    render() {
 
        console.log(this.state.year)   
        console.log(this.state.LGEONSPWReport)   

        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/'} />)
        }

        return (
            <div>
                <Header />
                <Menu />
                <div className="content-wrapper">
                    <br />
                    <section className="content">
                        <div className="container-fluid">

                            <div className="row">

                                <div className="col-12">

                                    <div className="card">
                                        <div className="card-header">
                                            <h3 className="card-title">LGAs Expenditure </h3>
                                        </div>
                                        {/* /.card-header */}
                                       
                                        <div className="card-body table-responsive p-0">
                                         <table id="emp"className="table table-hover text-nowrap">
                                                                                         
                                                <thead>
                                                
                                                <center><div style={{ textAlign: 'center' ,fontWeight:'bold'}}> LGAs Expenditure <br/> </div></center>
                                                <hr/>

                                      
                                                               <tr >
                                                               <th style={{ backgroundColor: '#29d050'}}>Category</th>   
                                                    <th>Capital</th>
                                                    <th>Recurrent</th>
                                                    <th style={{ backgroundColor: '#29d050'}}>Total</th> 

                                                   

                                                    </tr>
                                                   
                                                    
                                                </thead>
                                                <tbody>

                                                    {this.state.LGEONSPWReport && this.state.LGEONSPWReport.map(category => (
                                                        <tr>
                                                        <td>
                                                            <b>{category.category}
                                                                </b>                                                                    
                                                            </td>


                                                            <td>
                                                            {category.capital}

                                                            </td>
                                                            <td>
                                                            {category.recurrent}

                                                            </td>
                                                            <td style={{ backgroundColor: '#29d050'}}>
                                                            {category.total}

                                                            </td>


                                                            </tr>
                                                            
                                                            
      

                                                    ))}

                                                </tbody>

                                            </table>
                                            <div>
                                                <ReactHTMLTableToExcel
                                                    className="btn btn-info"
                                                    table="emp"
                                                    filename="PensiondeductionReport"
                                                    sheet="Sheet"
                                                    buttonText="Export excel" />
                                            </div>
                                            {/* */}

                                        </div>
                                    </div>
                                </div>


                            </div>


                        </div>


                    </section>
                </div>

                <Footer />
            </div>

        );
    }
}


