import axios from 'axios'
import React, { Component } from "react";
import {  Redirect } from 'react-router-dom';
import Header from './Header';
import Footer from './Footer'
import Spinner from 'react-bootstrap/Spinner'
import Menu from "./Menu";
// import ReactPaginate from 'react-paginate';



export default class ResponseByUser extends Component {

     constructor(props) {
          super(props);
          this.handlePageClick = this.handlePageClick.bind(this);

          this.state = {
               offset: 0,
               tableData: [],
               orgtableData: [],
               perPage: 10,
               currentPage: 0,
               myloading: true
          };
     }

     //pagination
     handlePageClick = (e) => {
          const selectedPage = e.selected;
          const offset = selectedPage * this.state.perPage;
          this.setState({
               currentPage: selectedPage,
               offset: offset
          }, () => {
               this.loadMoreData()
          });

     };
     //pagination
     loadMoreData() {
          const data = this.state.orgtableData;

          const slice = data.slice(this.state.offset, this.state.offset + this.state.perPage)
          this.setState({
               pageCount: Math.ceil(data.length / this.state.perPage),
               tableData: slice
          })

     }

     componentDidMount() {

          if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
               return (<Redirect to={'/login'} />)
             }
          const scriptdashboard = document.createElement("script");
          scriptdashboard.src = "./plugins/datatables-buttons/js/dataTables.buttons.min.js";
          scriptdashboard.async = true;
          document.body.appendChild(scriptdashboard);

         
          this.Get_State_Data();

     }

     Get_State_Data() {
          const mytoken = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
          const token = mytoken.token;
          // const Response_by_user = mytoken.user
          axios.get(`https://surveyng.cbn.gov.ng/api/survey/api/list/survey/`,
               {
                    headers:
                    {
                         'Content-Type': 'application/json',
                         'Authorization': `Token ${token}`,
                         'Access-Control-Allow-Origin': '*',
                         'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                         'Access-Control-Allow-Credentials': true
                    },
                    // params:{
                    //      survey_type=
                    // },

               },
          )
               .then(res => {
                    if (res.data) {
                         localStorage.setItem("List_Of_Responses_By_User", JSON.stringify(res.data));
                    }
                    const tdata = res.data;
                    // console.log(res.data.data)   

                    const slice = tdata.slice(this.state.offset, this.state.offset + this.state.perPage)
                    this.setState({
                         pageCount: Math.ceil(tdata.length / this.state.perPage),
                         orgtableData: tdata,
                         ResponseByUser: slice,
                         myloading: false
                    })
               });
          // console.log(res.data)   
     }

     render() {

          if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
               return (<Redirect to={'/'} />)
          }

          return (
               <div>
                    <Header />
                    <Menu />
                    <div className="content-wrapper">
                         <br />
                         <section className="content">
                              <div className="container-fluid">

                                   <div className="row">

                                        <div className="col-12">
                                             {this.state.myloading ? (
                                                  <>
                                                       <center><Spinner animation="border" variant="success" /></center>
                                                  </>
                                             ) : (

                                                  <div className="row">
                                                       <div className="col-12">
                                                            <div className="card">
                                                                 <div className="card-header">
                                                                      <h3 className="card-title">Responses</h3>
                                                                      <div className="card-tools">
                                                                           <div className="input-group input-group-sm" style={{ width: 150 }}>
                                                                                <input type="text" name="table_search" className="form-control float-right" placeholder="Search" />
                                                                                <div className="input-group-append">
                                                                                     <button type="submit" className="btn btn-default">
                                                                                          <i className="fas fa-search" />
                                                                                     </button>
                                                                                </div>
                                                                           </div>
                                                                      </div>
                                                                 </div>
                                                                 {/* /.card-header */}
                                                                 <div className="card-body table-responsive p-0">
                                                                      <table className="table table-hover text-nowrap">
                                                                           <thead>
                                                                                <tr>
                                                                                     <th>ID</th>
                                                                                     <th>User</th>
                                                                                     <th>Date</th>
                                                                                     <th>Status</th>
                                                                                     <th>Reason</th>
                                                                                </tr>
                                                                           </thead>
                                                                           <tbody>
                                                                                <tr>
                                                                                     <td>183</td>
                                                                                     <td>John Doe</td>
                                                                                     <td>11-7-2014</td>
                                                                                     <td><span className="tag tag-success">Approved</span></td>
                                                                                     <td>Bacon ipsum dolor sit amet salami venison chicken flank fatback doner.</td>
                                                                                </tr>
                                                                               
                                                                           </tbody>
                                                                      </table>
                                                                 </div>
                                                                 {/* /.card-body */}
                                                            </div>
                                                            {/* /.card */}
                                                       </div>
                                                  </div>

                                             )}
                                        </div>


                                   </div>


                              </div>


                         </section>
                    </div>

                    <Footer />
               </div>

          );
     }
}


