export default class SurveyAttachment extends Component {

    constructor(props) {
        super(props);
        
        this.onClickHandler = this.onClickHandler.bind(this);
        
        
        this.state = {
           
            loading: false,
            uploadPercentage: 0,
            uploadFilePercentage: 0,
            Filetitle: '',
            FileInfo: '',
            file: null,
            loadingFile: false,
            selectedFile: null,
            loaded: 0
        };
    }

 //My new Smart Upload  
 handleFileTitleChangeSmart =(e)=>{
    this.setState({
        Filetitle: e.target.value
    })
};

handleInfoChangeSmart= (e)=> {
    this.setState({
        FileInfo: e.target.value
    })
};
checkMimeType = (event) => {
    //getting file object
    let files = event.target.files
    //define message container
    let err = []
    // list allow mime type
    const types = ['application/vnd.ms-excel','application/vnd.openxmlformats-officedocument.spreadsheetml.sheet','application/pdf', 'application/doc','application/docx','application/xls','file/pdf', 'file/doc','file/docx','image/png', 'image/jpeg', 'image/gif']
    // loop access array
    for (let x = 0; x < files.length; x++) {
        // compare file type find doesn't matach
        if (types.every(type => files[x].type !== type)) {
            // create error message and assign to container   
            err[x] = files[x].type + ' is not a supported format\n';
        }
    };
    for (let z = 0; z < err.length; z++) {// if message not same old that mean has error 
        // discard selected file
        toast.error(err[z])
        event.target.value = null
    }
    return true;
}
maxSelectFile = (event) => {
    let files = event.target.files
    if (files.length > 3) {
        const msg = 'Only 3 images can be uploaded at a time'
        event.target.value = null
        toast.warn(msg)
        return false;
    }
    return true;
}
checkFileSize = (event) => {
    let files = event.target.files
    let size = 10000000
    let err = [];
    for (var x = 0; x < files.length; x++) {
        if (files[x].size > size) {
            err[x] = files[x].type + 'is too large, please pick a smaller file\n';
        }
    };
    for (var z = 0; z < err.length; z++) {// if message not same old that mean has error 
        // discard selected file
        toast.error(err[z])
        event.target.value = null
    }
    return true;
}
onChangeHandler = event => {
    var files = event.target.files
    if (this.maxSelectFile(event) && this.checkMimeType(event) && this.checkFileSize(event)) {
        // if return true allow to setState
        this.setState({
            selectedFile: files,
            loaded: 0
        })
    }
}
onClickHandler (e) {
    e.preventDefault();

     this.setState({
          message: "",
          loading: true,

        });
    const user = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
    const token= user.token 



    const Formdata = new FormData()
    // const Surveydetails = JSON.parse(localStorage.getItem('Response_Success'));
    
        const survey_response=JSON.parse(localStorage.getItem('Response_Success'));
        const pk= survey_response.survey_response_pk
    for (var x = 0; x<  this.state.selectedFile.length ; x++) {
        Formdata.append('document', this.state.selectedFile[x])
        Formdata.append('title', this.state.Filetitle);
        Formdata.append('info', this.state.FileInfo);
        Formdata.append('response', pk);
        
    }
    axios.post("https://surveyng.cbn.gov.ng/api/survey/api/add/survey-response/document/", Formdata, {
        
    headers: {
            'content-type': 'multipart/form-data',
            'Authorization': `Token ${token}`,
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Methods': 'GET,HEAD,OPTIONS',
            'Access-Control-Allow-Credentials': true
        },
        
        onUploadProgress: ProgressEvent => {
            this.setState({
                loaded: (ProgressEvent.loaded / ProgressEvent.total * 100),
            })
        },
    })
        .then(res => {
            // then print response status
          //   console.log(res)
            toast.success('File successfully uploaded')
          //   window.location = "/UserUploads"

          this.setState({
               message: "",
               loading: true,
     
             });
        })
        
        .catch(err => {
            // then print response status
            toast.error('Failed to Upload the File')
        })
}
//end file upload

   

    componentDidMount() {

     // const scriptdashboard = document.createElement("script");
     // scriptdashboard.src = "./plugins/dropzone/min/dropzone.min.js";
     // scriptdashboard.async = true;
     // document.body.appendChild(scriptdashboard); 
     // const scriptdashboardjquery = document.createElement("script");
     // scriptdashboardjquery.src = "./plugins/jquery/jquery.min.js";
     // scriptdashboardjquery.async = true;
     // document.body.appendChild(scriptdashboardjquery);
     //    console.log("hey im here",Surveydetails.pk)
        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }
        
    }


    render() {

        //check for user to access the page
        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }
        
        return (
            <div>
                    <br />
                    {this.state.AdoptSurveyloading && (
                        <span className="">
                            <div className="overlay-wrapper">
                                <div className="overlay ">         
                      <center> <img src={Logo} height="50px" alt="" /></center><br />
                             <div className="text-bold pt-2"> &nbsp;&nbsp;Please wait ...</div></div>
                            </div>

                        </span>
                     )}
                    
                            <div className="row">
                                {/* left column */}
                                <div className="col-md-12">
                                   
                                    <div className="card card-">
                                        <div className="card-header">
                                            <h3 className="card-title">Attach Document</h3>
                                        </div>
                                        <div className="card-body">
                                                                    <ToastContainer />

                                                                    <Progress max="100" color="success" value={this.state.loaded} >{Math.round(this.state.loaded, 2)}%</Progress>
                                                                    <br />
                                                                
                                                                    <div className="col-4">

                                                                        <label > Title</label>
                                                                        <input type="text" className="form-control"
                                                                         onChange={this.handleFileTitleChangeSmart} 
                                                                            required />
                                                                       </div>
                                                                       <div className="col-4">

                                                                            <label >Info</label>
                                                                        <input type="text" className="form-control" onChange={this.handleInfoChangeSmart} 
                                                                       required />
                                                                          </div>
                                                                          
                                                                          <div className="col-4">
                                                                        
                                                                      <input type="file" className="form-control" onChange={this.onChangeHandler} value={this.state.file}  
                                                                      required/>
                                                                      xls,xlsx,pdf,doc,docx,png,jpeg,  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                                                     
                                                                      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                                                         Max.size : 10mb

                                                                      </div>
                                                                        <br/>
                                                                        <button 
                                                                        type="button" 
                                                                        className="btn btn-info btn-block" 
                                                                    disabled={this.state.loaded}
                                                                    onClick={this.onClickHandler} > Attach <i class="nav-icon fas fa-paperclip text-white"></i>                                                        
                                                                                </button>
                                                                                <br/>
                                                                     <center> <Link to="/AvailableSurvey"className=" btn btn-primary btn-lg float-left " >
                                   
                                                                          <span>Back to Surveys</span>
                                                                              </Link></center> 
                                                    
                                                                    </div>
                                                                  
                                                                    
                                    </div>
                                    {/* /.card-body */}
                                </div>
                                {/* /.card */}
                            </div>
                            {/*/.col (right) */}
                            {/* /.row */}
                        
            </div>
        );
    }
}

