import axios from 'axios'
import React, { Component} from "react";
import { Link, Redirect } from 'react-router-dom';
import Header from './Header';
import Footer from './Footer'
import Spinner from 'react-bootstrap/Spinner'
import Menu from "./Menu";
import ReactPaginate from 'react-paginate';



export default class ListResponses extends Component {

    constructor(props) {
        super(props);
        this.handlePageClick = this.handlePageClick.bind(this);

        this.state = {
            offset: 0,
            tableData: [],
            orgtableData: [],
            perPage: 10,
            currentPage: 0,
            myloading: true
        };
    }

    //pagination
    handlePageClick = (e) => {
        const selectedPage = e.selected;
        const offset = selectedPage * this.state.perPage;
        this.setState({
            currentPage: selectedPage,
            offset: offset
        }, () => {
            this.loadMoreData()
        });

    };
    //pagination
    loadMoreData() {
        const data = this.state.orgtableData;

        const slice = data.slice(this.state.offset, this.state.offset + this.state.perPage)
        this.setState({
            pageCount: Math.ceil(data.length / this.state.perPage),
            tableData: slice
        })

    }

    componentDidMount() {
        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }
        // const scriptdashboard = document.createElement("script");
        // scriptdashboard.src = "./plugins/datatables-buttons/js/dataTables.buttons.min.js";
        // scriptdashboard.async = true;
        // document.body.appendChild(scriptdashboard);
    
       

        this.Get_State_Data();

    }

    Get_State_Data() {
        const mytoken = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
        const token = mytoken.token;
        axios.get(`https://surveyng.cbn.gov.ng/api/survey/api/list/response/`,
            {
                headers:
                {
                    'Content-Type': 'application/json',
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },

            },
        )
            .then(res => {
                if (res.data) {
                    localStorage.setItem("List_Of_Responses", JSON.stringify(res.data));
                }
                const tdata = res.data;
                // console.log(res.data.data)   

                const slice = tdata.slice(this.state.offset, this.state.offset + this.state.perPage)
                this.setState({
                    pageCount: Math.ceil(tdata.length / this.state.perPage),
                    orgtableData: tdata,
                    tableDataForState: slice,
                    myloading: false
                })
            });
        // console.log(res.data)   
    }

    render() {

        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/'} />)
        }

        return (
            <div>
                <Header />
                <Menu />
                <div className="content-wrapper">
                <br />     
                    <section className="content">
                        <div className="container-fluid">
                        
                            <div className="row">
                           
                                <div className="col-12">
                                {this.state.myloading ? (
                                <>
                                    <center><Spinner animation="border" variant="success" /></center>
                                </>
                            ) : (
                                    <div className="card">
                                        <div className="card-header">
                                            <h3 className="card-title">Filled Surveys</h3>
                                        </div>
                                        {/* /.card-header */}
                                        <div className="card-body">
                                            <div id="example1_wrapper" className="dataTables_wrapper dt-bootstrap4"><div className="row"><div className="col-sm-12 col-md-6">
                                           </div>
                                             </div><div className="row"><div className="col-sm-12"><table id="example1" className="table table-bordered table-striped dataTable dtr-inline" role="grid" aria-describedby="example1_info">
                                                <thead>
                                                    <tr role="row">
                                                    <th className="sorting" tabIndex={0} aria-controls="example1" rowSpan={1} colSpan={1} aria-label="Browser: activate to sort column ascending"> Title</th>
                                                    <th className="sorting" tabIndex={0} aria-controls="example1" rowSpan={1} colSpan={1} aria-label="Platform(s): activate to sort column ascending">Filled by</th>
                                                    <th className="sorting" tabIndex={0} aria-controls="example1" rowSpan={1} colSpan={1} aria-label="Engine version: activate to sort column ascending">Categories</th>
                                                    <th className="sorting sorting_asc" tabIndex={0} aria-controls="example1">Action</th>

                                                   </tr>
                                                </thead>
                                                <tbody className="table-body">
                                                    {this.state.tableDataForState && this.state.tableDataForState.map(survey => (

                                                        <tr>
                                                            <td>{survey.survey_title}</td>
                                                            <td>
                                                            {survey.filled_by}

                                                            </td>
                                                            
                                                            <td>
                                                                {survey.get_entries.map(category => (
                                                                 <td>
                                                                 {category.name}
                                                                </td>
                              
                                                               ))}
                                                            </td>
                                                            <td className="project-actions text-right">
                                            <Link className="btn btn-success btn-sm" to="#">
                                                <i className="fas fa-folder">
                                                </i>
                View
              </Link>&nbsp;&nbsp;
                             
                                        </td>
                                                          
                                                        </tr>

                                                    ))}

                                                </tbody>
                                                <tfoot>
                                                    
                                                </tfoot>
                                               
                                            </table>
                                            <ReactPaginate
                                                    previousLabel={"prev"}
                                                    nextLabel={"next"}
                                                    breakLabel={"..."}
                                                    breakClassName={"break-me"}
                                                    pageCount={this.state.pageCount}
                                                    marginPagesDisplayed={2}
                                                    pageRangeDisplayed={5}
                                                    onPageChange={this.handlePageClick}
                                                    containerClassName={"pagination"}
                                                    subContainerClassName={"pages pagination"}
                                                    activeClassName={"active"} />
                                            </div>
                                            </div>

                                            </div>
                                        </div>
                                    </div>
                                    )}
                                </div>

                                 
                            </div>
                                                      

                        </div>


                    </section>
                </div>

                <Footer />
            </div>

        );
    }
}


