import React, { Component } from "react";
import { Redirect, Link } from 'react-router-dom';
import Header from './Header';
import Footer from './Footer'
import axios from "axios";
import Spinner from 'react-bootstrap/Spinner'
import Menu from "./Menu";
import authService from "../services/auth.service";


export default class ValidateResponses extends Component {

    constructor(props) {
        super(props);
        this.state = {
            warningLoad: false,
            myloading: true,
            message: "",
            loading: false,
            Surveys: [],
        };
    }
    componentDidMount() {
        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }
        const mytoken = authService.getCurrentUser();
        const token = mytoken.token;
        axios.get("https://surveyng.cbn.gov.ng/api/survey/api/list/response/?status=---",
            { 
                headers:
                
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                if (res.data) {
                    localStorage.setItem("PendingResponses", JSON.stringify(res.data));
                }
                // (res);
                // (res.data);
                this.setState({
                    Surveys: res.data,
                    myloading: false,
                    warningLoad: true,
                });
            })

    }

    EditResponse(Surveys) {

        this.setState({
            loading: true,
  
          });       
        const mytoken = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
        const token = mytoken.token;
        const survey_pk = Surveys.pk;
        // console.log(Surveys.pk)


       
        axios.get(`https://surveyng.cbn.gov.ng/api/survey/api/details/response/${survey_pk}/`,
            {
                headers:
                {
                    'Content-Type': 'application/json',
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
               

            })
            .then(res => {
                if (res.data) {
                    localStorage.setItem("ResponseEDit", JSON.stringify(res.data));
                }
                // console.log(res.data)
                window.location = "/ResponseEdit"
                this.setState({
                    // loading: false,
                   
                });
            })
            // axios.get(`https://surveyng.cbn.gov.ng/api/survey/api/list/survey/document/${survey_pk}/`,
            // {
            //     headers:
            //     {
            //         'Content-Type': 'application/json',
            //         'Authorization': `Token ${token}`,
            //         'Access-Control-Allow-Origin': '*',
            //         'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
            //         'Access-Control-Allow-Credentials': true
            //     },
               

            // })
            // .then(res => {
            //     if (res.data) {
            //         localStorage.setItem("ResponseDocuments", JSON.stringify(res.data));
            //     }
            //     // console.log(res.data)                
            // })


    }

    render() {

        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/'} />)
        }


        return (
            <div>
                <Header />
                <Menu />
                {this.state.loading && (
                        <span className="">
                            <div className="overlay-wrapper">
                                <div className="overlay"><i className="fas fa-2x fa-sync-alt fa-spin" /></div>
                            </div>

                        </span>
                     )}
                <section className="content-wrapper" >
                
                    <br />
                    <div className="container-fluid">

                        {/* Default box */}
                        <div className="card">
                            <div className="card-header">

                                <h3 className="card-title">Responses</h3>
                                <div className="card-tools">
                                    <button type="button" className="btn btn-tool" data-card-widget="collapse" title="Collapse">
                                        <i className="fas fa-minus" />
                                    </button>
                                    <button type="button" className="btn btn-tool" data-card-widget="remove" title="Remove">
                                        <i className="fas fa-times" />
                                    </button>
                                </div>
                            </div>
                            {this.state.myloading ? (
                                <>
                                    <center><Spinner animation="border" variant="success" /></center>
                                </>
                            ) : (
                                <div className="card-body p-0">

                                    <table className="table table-striped projects">
                                        
                                        <thead>
                                            <tr>
                                                
                                            <th style={{ width: '0%' }}>
                                                    Survey Name
                                               </th>
                                               <th style={{ width: '0%' }}>
                                               filled by 
                                               </th>
                                               <th style={{ width: '0%' }}>
                                               State 
                                               </th>
                                               <th style={{ width: '0%' }}>
                                                    LGA
                                               </th>
                                               <th style={{ width: '0%' }} className="text-center">
                                                    Status
                                              </th>
                                               
                                               <th style={{ width: '0%' }}>
                                                      Quarter
                                                 </th> 
                                                 
                                                 <th style={{ width: '0%' }}>
                                                  Year
                                                 </th>
                                                 <th style={{ width: '0%' }}>
                                                  Type
                                                 </th>
                                                {/* <th>
                                                    Survey Progress
                                                   </th> */}
                                                
                                                <th style={{ width: '0%' }}>
                                                    Action
                                                </th>
                                            </tr>
                                        </thead>
                                        {this.state.Surveys.map(survey => (

                                            <tbody>
                                                <tr>
                                                    
                                                    <td>
                                                        <Link>
                                                            {survey.title}
                                                        </Link>
                                                        
                                                        <br />

                                                    </td>
                                                    <td>
                                                        <Link>
                                                        {survey.filled_by}

                                                        </Link>
                                                        
                                                        <br />
                                                        
                                                    </td>

                                                    <td>
                                                        <Link>
                                                        {survey.state_title}
                                                        </Link>

                                                    </td>
                                                    <td>
                                                        <Link>
                                                            {survey.lga_title}
                                                        </Link>

                                                    </td>
                                                    <td className="project-state">
                                                        <span className="badge badge-success">open</span>
                                                    </td>
                                                    <td>
                                                        <Link>
                                                            {survey.quarter}
                                                        </Link>

                                                    </td> 
                                                    <td>
                                                        <Link>
                                                            {survey.year}
                                                        </Link>

                                                    </td>
                                                      
                                                    <td>
                                                        <Link>
                                                            {survey.survey_type}
                                                        </Link>

                                                    </td>
                                                
                                                    <td className="project-actions text-right">
                                                        <Link
                                                         className="btn btn-primary btn-sm" 
                                                         onClick={this.EditResponse.bind(this, survey)}

                                                        >
                                                            
                                                     Approve
                                                    </Link>&nbsp;&nbsp;
 
                                                  </td>

                                                </tr>
                                            </tbody>
                                        ))}

                                    </table>
                                </div>

                            )}

                            {/* /.card-body */}
                        </div>
                        {/* /.card */}
                    </div>

                </section>


                <Footer />

            </div>
        );
    }
}