// import { Redirect } from 'react-router-dom';
import Header from './Header';
import Menu from './Menu';
import Footer from './Footer';
import axios from 'axios'
import authService from "../services/auth.service";
import AddField from "./AddField";
import React, {  useRef, useState, useEffect } from 'react';




export default function EditTemplate() {

    // const mySurvey_associate = JSON.parse(localStorage.getItem('ApprovedSurveyToUser'))
    // const [mySurvey, setMySurvey] = useState(mySurvey_associate);
    const [selectSurvey, setSelectSurvey] = useState("");
    // const [successful, setSuccessful] = useState(false);
    // const [message, setMessage] = useState("");
    // const [loading, setLoading] = useState(false);
  

    const onChangeMySurvey = (e) => {
        const selectSurvey = e.target.value;
        setSelectSurvey(selectSurvey);
        // localStorage.setItem("SelectedSurvey", JSON.stringify(mySurvey));

    };

    useEffect(() => {

        const mytoken = authService.getCurrentUser();
        const token = mytoken.token;
        axios.get("https://surveyng.cbn.gov.ng/api/survey/api/list/survey/?approved=yes&deployed=no",
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                if (res.data) {
                    localStorage.setItem("ApprovedSurveyToUser", JSON.stringify(res.data));
                }
                const selectSurvey = res.data
                setSelectSurvey(selectSurvey);

            })
    }, []);

    const childRef = useRef();
    const SingleTemplate = JSON.parse(localStorage.getItem('Templatedetails'));
    const SingleTemplateCategories = SingleTemplate.form;
    const TemplateTitle = SingleTemplate.title;

    return (
        <div>
            <Header />
            <div className="content-wrapper" style={{ minHeight: 1602 }}>
                <Menu /><br />
                <section className="content">
                    <div className="container-fluid">
                        <div className="row">
                            {/* left column */}
                            <div className="col-md-12">
                                <center><h4>{TemplateTitle}</h4></center>

                                {/* right column/ hannun dama */}
                                {/* /.card */}
                                {SingleTemplateCategories && SingleTemplateCategories.map((Templatefields, i) => (
                                    <div className="card card-">
                                        <div className="card-header">
                                            <h3 className="card-title">{Templatefields.economic_code}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<b>{Templatefields.name}</b>
                                            </h3>
                                            {/* <h6 className="">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{Templatefields.type}</h6> */}
                                            <h6 className="" style={{ fontStyle: 'italic' }}>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{Templatefields.heading}</h6>

                                        </div>
                                        <div className="card-body">
                                            {/* <h6 className="">{Templatefields.economic_code}</h6> */}
                                            {Templatefields.sub_categories.map(subCategory => (
                                                <div>
                                                    <h6 className="">{subCategory.name}</h6>

                                                    <div className="row">
                                                        {subCategory.fields.map(SubCategoryfields => (
                                                            <div className="col-4">
                                                                <h6 className="">{SubCategoryfields.economic_code}</h6>
                                                                <h6 className="">{SubCategoryfields.type}</h6>
                                                                <h6 className="">{SubCategoryfields.heading}</h6>

                                                                <input type="text" className="form-control is-valid" placeholder={SubCategoryfields.name} disabled />                                                <br />
                                                            </div>
                                                        ))}

                                                    </div>

                                                </div>
                                            ))}
                                            <hr />
                                            <div className="row">
                                                {Templatefields.fields.map((fields, i) => (

                                                    <div className="col-4">
                                                        <h6 className="">{fields.economic_code}</h6>
                                                        <h6 className="">{fields.type}</h6>
                                                        <h6 className="">{fields.heading}</h6>
                                                        <input type="text" className="form-control is-valid" placeholder={fields.name} disabled />                                                <br />

                                                    </div>


                                                ))}
                                            </div>
                                            <AddField key={i} ref={childRef}
                                             field={SingleTemplateCategories}
                                             MyCategoryName={Templatefields.name}
                                             fullTemplate={SingleTemplate}
                                            />

                                        </div>
                                        {/* /.card-body */}
                                    </div>
                                ))}

                                {/* /.card */}
                                {SingleTemplateCategories.length ?

                                    <div className="card card-">
                                        <div className="card-header">
                                            <h3 className="card-title">Action</h3>
                                        </div>
                                        <div className="card-body">
                                           
                                            <div>
                                                <div className="form-group">
                                                    <select
                                                        className="form-control"
                                                        id="exampleFormControlSelect9"
                                                        onChange={onChangeMySurvey}
                                                        value={selectSurvey}
                                                        required
                                                    >
                                                        <option value="">--select--</option>

                                                        {selectSurvey && selectSurvey.map(survey => (

                                                            <option value={survey.pk}>{survey.title}</option>

                                                        ))}

                                                    </select>
                                                </div>
                                      
                                            </div>
                                            <center>
                                            <button
                                                className="btn btn-success btn-lg "

                                                onClick={() => { childRef.current.handleSave() }}
                                            >
                                                Adopt Template
                                                </button>
                                                </center>
                                        </div>

                                    </div>
                                    : <center><h2>No Template Available</h2></center>

                                }
                                {/* /.card-body */}
                            </div>
                            {/* /.card */}
                        </div>
                        {/*/.col (right) */}
                        {/* /.row */}
                    </div>{/* /.container-fluid */}
                </section>
            </div>
            <Footer />
        </div>
    );
}




