import React, { Component } from "react";
import Form from "react-validation/build/form";
// import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
// import Textarea from "react-validation/build/textarea"
import Select from "react-validation/build/select"
// import { isEmail } from "validator";
// import { Link, Redirect } from 'react-router-dom';
// import AuthService from "../services/auth.service";
// import Spinner from 'react-bootstrap/Spinner'
import Header from "./Header";
import Menu from "./Menu";
// import Footer from "./Footer";
import axios from 'axios'
// import Logo from '../cbn.png'





const required = value => {
     if (!value) {
          return (
               <div className="alert alert-danger" role="alert">
                    This field is required!
               </div>
          );
     }
};



export default class SearchPensiondeduction extends Component {

     constructor(props) {
          super(props);
          this.handleGenerate = this.handleGenerate.bind(this);
          this.onChangeYear = this.onChangeYear.bind(this);
          this.onChangeSurvey_type = this.onChangeSurvey_type.bind(this);
       
          this.state = {
               year: "",
               successful: false,
               message: "",
               loading: false,
               survey_type:""

          };
     }


    
     onChangeYear(e) {
          this.setState({
               year: e.target.value
          });
     } 
     onChangeSurvey_type(e) {
          this.setState({
               survey_type: e.target.value
          });
     }
     
     
     handleGenerate(e) {
          e.preventDefault();
  
          this.setState({
               message: "",
              successful: false,
              loading: true
          });
  
          
              //check auth user
              const mytoken = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
              const token = mytoken.token;
  
          
              this.form.validateAll();
              if (this.checkBtn.context._errors.length === 0) {
      
              axios.get(`https://surveyng.cbn.gov.ng/api/survey/api/survey/total/by-year/?year=${this.state.year}&survey_type=${this.state.survey_type}`, 
             
                  {
                    headers:
                    {
                        'Content-Type': 'application/json',
                        'Authorization': `Token ${token}`,
                        'Access-Control-Allow-Origin': '*',
                        'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                        'Access-Control-Allow-Credentials': true
                    },
                   
                  })
                  .then(res => {
                      this.setState({
                          message: res.data.message,
                          successful: true,
                      });
                    //     console.log(res.data);
  
                      window.location = "/PensiondeductionReport"
                      if (res.data) {
                          localStorage.setItem("PensiondeductionReport", JSON.stringify(res.data));
                      }
                  },
                      error => {
                          const resMessage =
                              (error.response &&
                                  error.response.data.detail &&
                                  error.response.data.detail) ||
                              error.AdoptSurveyloading ||
                              error.toString();
  
                          this.setState({
                              loading: false,
                              message: resMessage
                          });
                      }
                  );
          } else {
              this.setState({
                  loading: false
              });
          }
      }
  



     render() {

          return (
               <div>
               <Header/>
                    <Menu/>
                    <div className="content-wrapper">
                    <br />
                    <section className="content">
                        <div className="container-fluid">

                            <div className="row">

                                <div className="col-12">

                                    <div className="card">
                                        <div className="card-header">
                                            <h3 className="card-title">Generate Report</h3>
                                        </div>
                                        {/* /.card-header */}
                                                                              {/* <p className="login-box-msg">Sign in to start your session</p> */}
                                        <br />
                                        <Form onSubmit={this.handleGenerate} ref={c => { this.form = c; }} className="form" id="">                          
                                        <center>
                                        <div className="col-7">

                                                                 <div className="form-group">
                                                                      <label>Survey Type</label>

                                                                      <Select
                                                                           className="form-control"
                                                                           id="exampleFormControlSelect9"
                                                                           validations={[required]}
                                                                           onChange={this.onChangeSurvey_type}
                                                                           value={this.state.survey_type}
                                                                      >
                                                                           <option value="">--select--</option>
                                                                           <option value="sg">SG</option>
                                                                           <option value="lg">LG</option>
                                             
                                                                      </Select>
                                                                 </div>
                                                            </div><div className="col-7">

                                                                 <div className="form-group">
                                                                      <label>Year</label>

                                                                      <Select
                                                                           className="form-control"
                                                                           id="exampleFormControlSelect9"
                                                                           validations={[required]}
                                                                           onChange={this.onChangeYear}
                                                                           value={this.state.year}
                                                                      >
                                                                           <option value="">--select--</option>
                                                                           <option value="2011">2011</option>
                                                                           <option value="2012">2012</option>
                                                                           <option value="2013">2013</option>
                                                                           <option value="2014">2014</option>
                                                                           <option value="2015">2015</option>
                                                                           <option value="2016">2016</option>
                                                                           <option value="2017">2017</option>
                                                                           <option value="2018">2018</option>
                                                                           <option value="2019">2019</option>
                                                                           <option value="2020">2020</option>
                                                                           <option value="2021">2021</option>

                                                                      </Select>
                                                                 </div>
                                                            </div>
                                             
                              <br/>
                                            
                                             
                                             <div className="form-group">
                                                  <button
                                                       className="btn btn-primary btn-lg"
                                                       disabled={this.state.loading}
                                                  >
                                                       
                                                       <span>Generate</span>&nbsp;&nbsp;
                                                       {this.state.loading && (
                                                            <span className="spinner-border spinner-border-sm"></span>
                                                       )}
                                                  </button>
                                             </div>
                                             <center>
                                                  {this.state.message && (
                                                       <div className="form-group">
                                                            <div
                                                                 className={
                                                                      this.state.successful
                                                                           ? "alert alert-success"
                                                                           : "alert alert-danger"
                                                                 }
                                                                 role="alert"
                                                            >
                                                                 {this.state.message}
                                                            </div>
                                                       </div>
                                                  )}
                                             </center>
                                             <CheckButton
                                                  style={{ display: "none" }}
                                                  ref={c => {
                                                       this.checkBtn = c;
                                                  }}
                                             />
                                                  </center>

                                        </Form>
                                                  
                                   </div>

                              </div>
                              </div>
                              </div>
                    </section>
                    </div>

               </div>


          );
     }
}