export default class Notifications extends Component {

  constructor(props) {
    super(props);


    this.state = {
      // message: "",
      loading: true,
      AdoptSurveymessage: "",
      AdoptSurveysuccessful: false,
      AdoptSurveyloading: false,
      survey: "",
      true:"True",
      offset: 0,
      message: [],
      perPage: 10,
      currentPage: 0,

    };
  }

  //pagination
  handlePageClick = (e) => {
    const selectedPage = e.selected;
    const offset = selectedPage * this.state.perPage;
    this.setState({
      currentPage: selectedPage,
      offset: offset
    }, () => {
      this.loadMoreData()
    });

  };
  //pagination
  loadMoreData() {
    const data = this.state.message;

    const slice = data.slice(this.state.offset, this.state.offset + this.state.perPage)
    this.setState({
      pageCount: Math.ceil(data.length / this.state.perPage),
      mymessage: slice
    })

  }


  componentDidMount() {
    if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
      return (<Redirect to={'/login'} />)
    }
    const mytoken = authService.getCurrentUser();
    const token = mytoken.token;
    axios.get('https://surveyng.cbn.gov.ng/api/core/api/user/notification/list/?read=no',
      {
        headers:
        {
          'Content-Type': 'application/json',
          'Authorization': `Token ${token}`,
        },

      },
    )
      .then(res => {
        if (res.data) {
          localStorage.setItem("notification", JSON.stringify(res.data));
        }
        const tdata = res.data;
        const slice = tdata.slice(this.state.offset, this.state.offset + this.state.perPage)

            this.setState({
              message: tdata,
              pageCount: Math.ceil(tdata.length / this.state.perPage),
              mymessage: slice,
              loading: false,
              notification: res.data.length,

            });
        
        // console.log(res.data)                  

      })

  }

  ViewNotification(message){

   
    const notifyId = message.pk
    // console.log(message.pk)
    // console.log(this.state.true)
    localStorage.setItem("notify_Update_ID", JSON.stringify(message.pk));
    const mytoken = authService.getCurrentUser();
    const token = mytoken.token;

    this.setState({
        loadingModal: true,
      });

    axios.get(`https://surveyng.cbn.gov.ng/api/core/api/user/notification/details/${notifyId}/`,
        {
            headers:
            {
                'Authorization': `Token ${token}`,
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                'Access-Control-Allow-Credentials': true
            },
        })
        .then(res => {
            // window.location = "/ViewNotification"

            if (res.data) {
                localStorage.setItem("ApproveNotifications", JSON.stringify(res.data));
            }

            this.setState({
              ApproveThisSurvey: res.data,
              loadingModal: false,
            });

          })

          const  Read = new FormData();

          Read.append('read', this.state.true);
          
          axios.patch(`https://surveyng.cbn.gov.ng/api/core/api/user/notification/update/${notifyId}/`,Read,
            {
            headers:
            {
                'Authorization': `Token ${token}`,
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                'Access-Control-Allow-Credentials': true
            },
        })
        .then(res => {
            // if (res.data) {
            //     localStorage.setItem("Read", JSON.stringify(res.data));
            // }

          })

  }


  render() {

    //check for user to access the page
    if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
      return (<Redirect to={'/login'} />)
    }
    //get the template details
    // const ApproveNotifications = JSON.parse(localStorage.getItem('ApproveNotifications'));


    return (
      <div>
        <Header />
        <div className="content-wrapper" style={{ minHeight: 1602 }}>
          <Menu /><br />
          <section className="content">
            <div className="row">
              <div className="col-md-3">
                <Link className="btn btn-success btn-block mb-3">Notification</Link>
               
                <div className="card">
                  <div className="card-header">
                    <h3 className="card-title">Folders</h3>
                    <div className="card-tools">
                      <button type="button" className="btn btn-tool" data-card-widget="collapse">
                        <i className="fas fa-minus" />
                      </button>
                    </div>
                  </div>
                  <div className="card-body p-0">
                    <ul className="nav nav-pills flex-column">
                      <li className="nav-item active">
                        <Link to="/Notifications" className="nav-link">
                          <i className="fas fa-inbox" /> New
                <span className="badge bg-primary float-right">{this.state.notification}</span>
                        </Link>
                      </li>
                      <li className="nav-item">
                        <Link to="#" className="nav-link">
                          <i className="far fa-envelope" /> Read
              </Link>
                      </li>
                    
                      {/* <li className="nav-item">
                        <Link to="#" className="nav-link">
                          <i className="far fa-trash-alt" /> Trash
              </Link>
                      </li> */}
                    </ul>
                  </div>
                  {/* /.card-body */}
                </div>
               
              </div>
              {/* /.col */}
              <div className="col-md-9">
                <div className="card card-success card-outline">
                  <div className="card-header">
                    <h3 className="card-title">Notifications</h3>
                    <div className="card-tools">
                     
                    </div>
                    {/* /.card-tools */}
                  </div>
                  {/* /.card-header */}
                  <div className="card-body p-0">
                    
                    <div className="table-responsive mailbox-messages">
                      {this.state.loading ? (
                        <span className="">
                            <div className="overlay-wrapper">
                                <div className="overlay ">         
                      <center> <img src={Logo} height="50px" alt="" /></center><br />
                             <div className="text-bold pt-2"> &nbsp;&nbsp;Please wait ...</div></div>
                            </div>

                        </span> 
                        ) : (
                      <table className="table table-hover table-striped">
                        <tbody>
                        {this.state.mymessage && this.state.mymessage.map(message => (
                          <tr>
                            <td>
                              <div className="icheck-primary">
                                <input type="checkbox" defaultValue id="check1" />
                                <label htmlFor="check1" />
                              </div>
                            </td>
                            <td className="mailbox-star"><Link to="#"><i className="fas fa-star text-warning" /></Link></td>
                            <td className="mailbox-name">
                            <Link
                            onClick={this.ViewNotification.bind(this, message)}
                            >{message.subject}</Link></td>
                            {/* <td className="mailbox-subject"><b>{message.api_message}</b> -         </td> */}
                            <td className="mailbox-attachment" />
                            <td className="mailbox-date">{message.created}</td>
                          </tr>
                        ))}
                           </tbody>
                           <ReactPaginate
                            previousLabel={"prev"}
                            nextLabel={"next"}
                            breakLabel={"..."}
                            breakClassName={"break-me"}
                            pageCount={this.state.pageCount}
                            marginPagesDisplayed={2}
                            pageRangeDisplayed={5}
                            onPageChange={this.handlePageClick}
                            containerClassName={"pagination"}
                            subContainerClassName={"pages pagination"}
                            activeClassName={"active"} />
                      </table>
                      
                        )}
                    </div>
                    {/* /.mail-box-messages */}
                  </div>
                  {/* /.card-body */}
                  <div className="card-footer p-0">
                    
                  </div>
                </div>
                {/* /.card */}
              </div>
              {/* /.col */}
            </div>
            {/* /.row */}
          </section>

        </div>
        <Footer />
      </div>
    );
  }
}



