// };
export default class PendingResponsesResponder extends Component {

    constructor(props) {
        super(props);
       
        this.state = {
            warningLoad: false,
            myloading: true,
            loading:false,
            message: "",
            PendingResponsesResponder: [],
          
        };
    }
    
   
    componentDidMount() {
     if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
          return (<Redirect to={'/login'} />)
        }
        const Check=JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
    const token = Check.token

            const user_type=Check.profile.user_type
            const lga =Check.profile.lga
            const state =Check.profile.state
            // const Hq = state && lga

            switch (user_type) {
                case 'sg_user':
                    return   axios.get(`https://surveyng.cbn.gov.ng/api/survey/api/list/response/?status=---&states=${state}&user_responded=yes&for_user=yes`,
                    {
                        headers:
                        {
                            'Authorization': `Token ${token}`,
                            'Access-Control-Allow-Origin': '*',
                            'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                            'Access-Control-Allow-Credentials': true
                        },
                    })
                    .then(res => {
                        this.setState({
                            Surveys: res.data,
                            myloading: false,
                            warningLoad: true,
                        });
                    })
             
                case 'lg_user':
                    return   axios.get(`https://surveyng.cbn.gov.ng/api/survey/api/list/response/?status=---&states=${lga}&user_responded=yes&for_user=yes`,
                    {
                        headers:
                        {
                            'Authorization': `Token ${token}`,
                            'Access-Control-Allow-Origin': '*',
                            'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                            'Access-Control-Allow-Credentials': true
                        },
                    })
                    .then(res => {
                        this.setState({
                            Surveys: res.data,
                            myloading: false,
                            warningLoad: true,
                        });
                    })
                    case 'hq_user':
                         return   axios.get(`https://surveyng.cbn.gov.ng/api/survey/api/list/response/?status=---&user_responded=yes`,
                         {
                        headers:
                        {
                            'Authorization': `Token ${token}`,
                            'Access-Control-Allow-Origin': '*',
                            'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                            'Access-Control-Allow-Credentials': true
                        },
                    })
                    .then(res => {
                        this.setState({
                            Surveys: res.data,
                            myloading: false,
                            warningLoad: true,
                        });
                    })
        
                default:
                    return null;
            }
        
    }

  

    render() {

        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/'} />)
        }


        return (
            <div>
                <Header />
                <Menu />
                <div className="content-wrapper">
                         <br />
                         {this.state.loadingModal && (
                        <span className="">
                            <div className="overlay-wrapper">
                                <div className="overlay ">         
                      <center> <img src={Logo} height="50px" alt="" /></center><br />
                             <div className="text-bold pt-2"> &nbsp;&nbsp;Please wait ...</div></div>
                            </div>

                        </span>
                     )}
                <section className="content">
                              <div className="container-fluid">

                                   <div className="row">

                                        <div className="col-12">
                                             {this.state.myloading ? (
                                                  
                                                       <center><Spinner animation="border" variant="success" /></center>
                                             ) : (

                                                  <div className="row">
                                                       <div className="col-12">
                                                            <div className="card">
                                                                 <div className="card-header">
                                                                      <h3 className="card-title">Pending Responses</h3>
                                                                      <div className="card-tools">
                                                                           
                                                                      </div>
                                                                 </div>
                                                                 {/* /.card-header */}
                                                                 {this.state.myloading ? (
                                                                 <center><Spinner animation="border" variant="success" /></center>
                                                                  ) : (
                                                                 <div className="card-body table-responsive p-0">
                                                                      <table className="table table-hover text-nowrap">
                                                                           <thead>
                                                                                <tr>
                                                                                <th>Title</th>
                                                                                     <th>Year</th>
                                                                                     <th>Quarter</th>
                                                                                     <th>Created by</th>
                                                                                     <th>Status </th>
                                                                                </tr>
                                                                           </thead>
                                                                           {this.state.Surveys.map(survey => (

                                                                           <tbody>
                                                                                <tr>
                                                                                <td>{survey.title}</td>
                                                                                     <td>{survey.year}</td>
                                                                                     <td>{survey.quarter}</td>
                                                                                     <td><span className="tag tag-success">{survey.created_by}</span></td>
                                                                                     <td className="">
                                                                                           <button
                                                                                               className="btn btn-warning"
                                                                                               disabled={this.state.loading}

                                                                                           >Pending
                                                                                         </button>
                                                                                           
                                                                                         
                                                                                         </td>
                                             

                                                                                </tr>
                                                                               
                                                                           </tbody>
                                                                           ))}
                                                                      </table>
                                                                 </div>
                                                                )}
                                                                 {/* /.card-body */}
                                                            </div>
                                                            {/* /.card */}
                                                       </div>
                                                  </div>

                                             )}
                                        </div>


                                   </div>


                              </div>


                         </section>

                </div>
                <Footer />

                </div>
        );
    }
}

             