import React, { Component } from "react";
import { Redirect, Link } from 'react-router-dom';
import Form from "react-validation/build/form";
// import Input from "react-validation/build/input";
import Textarea from "react-validation/build/textarea";
import CheckButton from "react-validation/build/button";
import Select from "react-validation/build/select";
import Header from './Header';
import Footer from './Footer'
import axios from "axios";
// import Spinner from 'react-bootstrap/Spinner'
import Menu from "./Menu";
import AuthService from "../services/auth.service";
// import Logo from '../cbn.png'

const required = (value) => {
    if (!value) {
         return (
              <div className="alert alert-danger" role="alert">
                   This field is required!
              </div>
         );
    }
};


export default class DeploySurvey extends Component {

    constructor(props) {
        super(props);
        
        this.handleApprove = this.handleApprove.bind(this);
        this.onChangestatus = this.onChangestatus.bind(this);
        this.onChangeRemark = this.onChangeRemark.bind(this);

        this.state = {
            warningLoad: false,
            myloading: true,
            loading: false,
            successful: false,
            message: "",
            Surveys: [],
            ApproveThisSurvey: [],
            status: "",
            remark: "",
            DeploySingleSurvey: []
        };
    }

   

    

    componentDidMount() {
     if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
          return (<Redirect to={'/login'} />)
        }
    }
    onChangestatus(e) {
        this.setState({
          status: e.target.value
        });
   }
   onChangeRemark(e) {
        this.setState({
             remark: e.target.value
        });
   }
   handleApprove(e) {

    e.preventDefault();

    const DeploySingleSurvey = JSON.parse(localStorage.getItem('DeployTemplateInfo'));

    const pk=DeploySingleSurvey.pk

    this.setState({
         message: "",
         successful: false,
         loading: true
    });
    this.form.validateAll();

    if (this.checkBtn.context._errors.length === 0) {

     let formData = new FormData();
     formData.append('status', this.state.status);
     formData.append('survey',  pk);
     formData.append('remark', this.state.remark);
     //     console.log(formData) 

         const mytoken = AuthService.getCurrentUser();
         const token = mytoken.token;
         axios.post(`https://surveyng.cbn.gov.ng/api/survey/api/history/survey/create/`, formData,

              {
                   headers:
                   {
                        'Authorization': `Token ${token}`,
                        'Access-Control-Allow-Origin': '*',
                        'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                        'Access-Control-Allow-Credentials': true
                   },
              }).then(

                   response => {

                        this.setState({
                             message: response.data.detail,
                             successful: true
                        });
                    //     window.location = "/SurveyToDeploy"
                   },
                   error => {
                        const resMessage =
                             (error.response &&
                                  error.response.data.detail &&
                                  error.response.data) ||
                             error.message ||
                             error.toString();

                        this.setState({
                             loading: false,
                             message: resMessage,
                             ViewTemplateloading: false,

                        });
                   }
              );
    } else {
         this.setState({
              loading: false
         });
    }
}
    

    render() {

        //check for user to access the page
        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }
        //get the template details
        const DeploySingleSurvey = JSON.parse(localStorage.getItem('DeployTemplateInfo'));
        const DeploySingleSurveyCategories = DeploySingleSurvey.form;
        

        return (
            <div>
                <Header />
                <div className="content-wrapper" style={{ minHeight: 1602 }}>
                    <Menu /><br />
                    <section className="content">
                        <div className="container-fluid">
                            <div className="row">
                                {/* left column */}
                                <div className="col-md-12">
                                    {/* right column/ hannun dama */}
                                    {/* /.card */}
                                    {DeploySingleSurveyCategories&&DeploySingleSurveyCategories.map(Templatefields => (
                                        <div className="card card-">
                                            <div className="card-header">
                                                <h3 className="card-title">{Templatefields.name}</h3>
                                            </div>
                                            <div className="card-body">
                                                {/* <h6 className="">{Templatefields.type}</h6> */}

                                                <div className="row">
                                                    {Templatefields.fields.map(fields => (

                                                        <div className="col-4">
                                                            <input type="text" className="form-control is-valid" placeholder={fields.name} disabled  />                                                <br />
                                                        </div>
                                                    ))}
                                                </div>
                                                {Templatefields.sub_categories.map(subCategory => (
                                                    <div>
                                                        <h6 className="">{subCategory.name}</h6>
                                                        <div className="row">
                                                            {subCategory.fields.map(SubCategoryfields => (
                                                                <div className="col-4">
                                                                    <input type="text" className="form-control is-valid" placeholder={SubCategoryfields.name} disabled  />                                                <br />
                                                                </div>
                                                            ))}
                                                        </div>
                                                    </div>
                                                ))}
                                                
                                            </div>
                                            {/* /.card-body */}

                                        </div>
                                    ))}
                                    <div className="card card-">
                                            <div className="card-header">
                                                <h3 className="card-title">{DeploySingleSurvey.title}</h3>
                                            </div>
                                            <div className="card-body">
                                                       {/* /.card-header */}
                                                       <div className="row">
                                                            <div className="col-sm-12">
                                                                 
                                                                      <label> Title</label>:&nbsp;
                                                                      <center>
                                                                                      {DeploySingleSurvey.title.toUpperCase()}

                                                                 </center>
                                                                 <hr />
                                                            </div>

                                                            <div className="col-sm-12">
                                                                 
                                                                      <label> Quarter</label>:&nbsp;
                                                                      <center>
                                                                      {DeploySingleSurvey.quarter.toUpperCase()}
                                                                 </center>
                                                                 <hr />
                                                            </div>
                                                       </div>
                                                       <div className="row">
                                                            <div className="col-sm-12">
                                                                 
                                                                      {/* text input */}
                                                                      <label> Year</label>:&nbsp;
                                                                      <center>
                                                                                      {DeploySingleSurvey.year}
                                                                 </center>
                                                                 <hr />

                                                            </div>
                                                            <div className="col-sm-12">
                                                                 {/* text input */}
                                                                 
                                                                      <label> Start date</label>:&nbsp;
                                                                      <center>
                                                                                       {DeploySingleSurvey.start_date}
                                                                 </center>
                                                                 <hr />

                                                            </div>
                                                            <div className="col-sm-12">
                                                                
                                                                      <label> Due Date</label>:&nbsp;
                                                                      <center>
                                                                                         {DeploySingleSurvey.due_date}

                                                                 </center>
                                                                 <hr />

                                                            </div>
                                                            <div className="col-sm-12">
                                                                 
                                                                      <label> Survey Type</label>:&nbsp;
                                                                      <center>
                                                                       {DeploySingleSurvey.survey_type.toUpperCase()}
                                                                 </center>
                                                                 {/* <hr /> */}
                                                            </div>
                                                            <div className="col-sm-12">
                                                                 <div className="table-container">
                                                                      <table className="table">
                                                                           <thead>
                                                                                <tr>
                                                                                     <th>STATE</th>                                                 
                                                                                </tr>
                                                                           </thead>
                                                                           <tbody className="table-body">

                                                                                     <tr>
                                                                                          {/* <td style={{ color: 'green', fontWeight: 'bold' }}>{lga.title} </td> */}
                                                                                          <br />
                                                                                          {DeploySingleSurvey.get_states && DeploySingleSurvey.get_states.map(states => (
                                                                                               <tr>
                                                                                                    <td>{states.title}</td>
                                                                                                    
                                                                                               </tr>
                                                                                          ))}
                                                                                     </tr>
                                                                           </tbody>
                                                                      </table>
                                                                 </div>
                                                                
                                                            </div>
                                                            <div className="col-sm-12">
                                                                 <div className="form-group">
                                                                      <label> LGAs</label>:&nbsp;
                                                                    {/* {DeploySingleSurvey.get_lgas} */}
                                                                      {DeploySingleSurvey.get_lgas && DeploySingleSurvey.get_lgas.map(states => (
                                                                           <span> {states.title}</span>
                                                                      ))}
                                                                 </div>
                                                            </div>
                                                       </div>
                                                  </div>
                                            {/* /.card-body */}
                                            
                                        </div>
                                    {/* /.card */}
                                    {DeploySingleSurveyCategories.length ? 

                                    <div className="card card-">
                                        <div className="card-header">
                                            <h3 className="card-title">Action</h3>
                                        </div>
                                        <div className="card-body">


                                        <Form
                                                       onSubmit={this.handleApprove}
                                                       ref={c => {
                                                            this.form = c;
                                                       }}
                                                  >
                                                       {!this.state.successful && (


                                                       <div>
                                                       <div className="card-body">
                                                       
                                                            <div className="form-group">
                                                                
                                                                 <Select
                                                                      className="form-control"
                                                                      id="exampleFormControlSelect9"
                                                                      validations={[required]}
                                                                      onChange={this.onChangestatus}
                                                                      value={this.state.status}
                                                                 >
                                                                      <option>--select--</option>
                                                                      <option value="approved_deploy">Approve</option>
                                                                      <option value="rejected_deploy">Reject</option>

                                                                 </Select>
                                                            </div>

                                                            <div className="form-group">
                                                                 <Textarea type="remark" name="remark" className="form-control" placeholder="Remark" value={this.state.remark}
                                                                      onChange={this.onChangeRemark}
                                                                      validations={[required]} />

                                                            </div>
                                                            <br />


                                                            <div className="form-group">
                                                                 <button
                                                                      className="btn btn-primary btn-lg"
                                                                      disabled={this.state.loading}
                                                                 >
                                                                      {/* {this.state.loading && (
                                            <span className="spinner-border spinner-border-sm"></span>
                                        )} */}
                                                                      <span>Submit</span>
                                                                 </button>
                                                            </div>
                                                            </div>
                                                            </div>
                                                              )}
                                                            
                                                            <CheckButton
                                                                 style={{ display: "none" }}
                                                                 ref={c => {
                                                                      this.checkBtn = c;
                                                                 }}
                                                            />

                                                       
                                                  </Form>
                               
                                                  <center>
                                                                 {this.state.message && (
                                                                      <div className="form-group">
                                                                           <div
                                                                                className={
                                                                                     this.state.successful
                                                                                          ? "alert alert-success"
                                                                                          : "alert alert-danger"
                                                                                }
                                                                                role="alert"
                                                                           >
                                                                                {this.state.message}
                                                                           </div>
                                                                           {this.state.successful &&
                                                                                     <Link
                                                                                          className="btn btn-primary"
                                                                                          to="/SurveyToDeploy"

                                                                                     >
                                                                                          continue
                                                                                              </Link>
                                                                                }


                                                                      </div>
                                                                 )}

                                                            </center>
                                          
                                       
                                        
                                          </div>
                                                                                
                                    </div>
                                    : <center><h2>No Template Available</h2></center>
                                        
                                }
                                    {/* /.card-body */}
                                
                                </div>
                               
                                {/* /.card */}
                            </div>
                            {/*/.col (right) */}
                            {/* /.row */}
                        </div>{/* /.container-fluid */}
                    </section>
                </div>
                <Footer />
            </div>
        );
    }
}



