export default class UploadSupporting extends Component {

    constructor(props) {
        super(props);
        
        // this.AdoptSurvey = this.AdoptSurvey.bind(this);

        this.state = {
           
            loading: false,
            uploadPercentage: 0,
            uploadFilePercentage: 0,
            Filetitle: '',
            FileInfo: '',
            file: null,
            loadingFile: false,
            selectedFile: null,
            loaded: 0
        };
    }

 //My new Smart Upload  
 handleFileTitleChangeSmart = (e) => {
    this.setState({
        Filetitle: e.target.value
    })
};

handleInfoChangeSmart = (e) => {
    this.setState({
        FileInfo: e.target.value
    })
};
checkMimeType = (event) => {
    //getting file object
    let files = event.target.files
    //define message container
    let err = []
    // list allow mime type
    const types = ['application/pdf', 'application/doc','application/docx','file/pdf', 'file/doc','file/docx','image/png', 'image/jpeg', 'image/gif']
    // loop access array
    for (let x = 0; x < files.length; x++) {
        // compare file type find doesn't matach
        if (types.every(type => files[x].type !== type)) {
            // create error message and assign to container   
            err[x] = files[x].type + ' is not a supported format\n';
        }
    };
    for (let z = 0; z < err.length; z++) {// if message not same old that mean has error 
        // discard selected file
        toast.error(err[z])
        event.target.value = null
    }
    return true;
}
maxSelectFile = (event) => {
    let files = event.target.files
    if (files.length > 3) {
        const msg = 'Only 3 images can be uploaded at a time'
        event.target.value = null
        toast.warn(msg)
        return false;
    }
    return true;
}
checkFileSize = (event) => {
    let files = event.target.files
    let size = 5000000
    let err = [];
    for (var x = 0; x < files.length; x++) {
        if (files[x].size > size) {
            err[x] = files[x].type + 'is too large, please pick a smaller file\n';
        }
    };
    for (var z = 0; z < err.length; z++) {// if message not same old that mean has error 
        // discard selected file
        toast.error(err[z])
        event.target.value = null
    }
    return true;
}
onChangeHandler = event => {
    var files = event.target.files
    if (this.maxSelectFile(event) && this.checkMimeType(event) && this.checkFileSize(event)) {
        // if return true allow to setState
        this.setState({
            selectedFile: files,
            loaded: 0
        })
    }
}
onClickHandler = () => {

    const user = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
    const token= user.token 

    const Formdata = new FormData()

    for (var x = 0; x < this.state.selectedFile.length; x++) {
        Formdata.append('document', this.state.selectedFile[x])
        Formdata.append('title', this.state.Filetitle);
        Formdata.append('info', this.state.FileInfo);
    }
    axios.post("https://surveyng.cbn.gov.ng/api/accounts/api/registration/supporting-docs/add/", Formdata, {
        headers: {
            'content-type': 'multipart/form-data',
            'Authorization': `Token ${token}`,
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Methods': 'GET,HEAD,OPTIONS',
            'Access-Control-Allow-Credentials': true
        },
        onUploadProgress: ProgressEvent => {
            this.setState({
                loaded: (ProgressEvent.loaded / ProgressEvent.total * 100),
            })
        },
    })
        .then(res => {
            // then print response status
            console.log(res)
            toast.success('File successfully uploaded')
            window.location = "/UserUploads"

        })
        .catch(err => {
            // then print response status
            toast.error('Failed to Upload the File')
        })
}

//end file upload

   

    componentDidMount() {

        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }
        
    }


    render() {

        //check for user to access the page
        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }
        
        return (
            <div>
                <Header />
                <div className="content-wrapper" style={{ minHeight: 1602 }}>
                    <Menu /><br />
                    {this.state.AdoptSurveyloading && (
                        <span className="">
                            <div className="overlay-wrapper">
                                <div className="overlay ">         
                      <center> <img src={Logo} height="50px" alt="" /></center><br />
                             <div className="text-bold pt-2"> &nbsp;&nbsp;Please wait ...</div></div>
                            </div>

                        </span>
                     )}
                    <section className="content">
                        <div className="container-fluid">
                            <div className="row">
                                {/* left column */}
                                <div className="col-md-12">
                                   
                                    <div className="card card-">
                                        <div className="card-header">
                                            <h3 className="card-title">Upload Supporting Document</h3>
                                        </div>
                                        <div className="card-body">
                                            <Form>
                                                                    <ToastContainer />
                                                                    <Progress max="100" color="success" value={this.state.loaded} >{Math.round(this.state.loaded, 2)}%</Progress>
                                                                    <br />
                                                                    <div className="form-group">
                                                                    <div className="col-4">

                                                                        <label > Title</label>
                                                                        <input type="text" className="form-control" onChange={this.handleFileTitleChangeSmart} 
                                                                            required />
                                                                        </div>
                                                                        <div className="col-4">

                                                                        <label >Info</label>
                                                                        <input type="text" className="form-control" onChange={this.handleInfoChangeSmart} 
                                                                        required />
                                                                        </div>

                                                                        <br/>
                                                                        <div className="col-4">

                                                                        <input type="file" class="form-control" onChange={this.onChangeHandler} value={this.state.file}  />
                                                                        
                                                                        </div>
                                                                        </div>
                                                                    <button type="button" class="btn btn-success" onClick={this.onClickHandler} >Upload</button>

                                                                    </Form> 
                                                                    </div>
                                    </div>
                                    {/* /.card-body */}
                                </div>
                                {/* /.card */}
                            </div>
                            {/*/.col (right) */}
                            {/* /.row */}
                        </div>{/* /.container-fluid */}
                    </section>
                </div>
                <Footer />
            </div>
        );
    }
}

