
import { useState, useEffect, useRef } from 'react';
import { Redirect,Link } from 'react-router-dom';
import FormElementRes from './FormElementRes';
import FormElementCatRes from './FormElementCatRes';
import { FormContext } from './FormContext';
import Header from './Header';
import Footer from './Footer'
import Menu from "./Menu";
import axios from 'axios'
// import CheckSurveyTypeResponse from './CheckSurveyTypeResponse';
import Form from "react-validation/build/form";
// import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
// import SurveyAttachment from './SurveyAttachment';



//hey i will use react hook here
const ResponseEdit = (props) => {

  
  const form = useRef();
  const checkBtn = useRef();

  const [Get_Survey_Details, Set_Survey_Details] = useState({});
  const [successful, setSuccessful] = useState(false);
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);
  

  // Call_Survey_Details form localStorage
  const ResponseEDit = JSON.parse(localStorage.getItem('ResponseEDit'));
  const Call_Survey_Details = ResponseEDit.get_entries;


  
  useEffect(() => {
    // Call_Survey_Details form localStorage
    const ResponseEDit = JSON.parse(localStorage.getItem('ResponseEDit'));
    const Call_Survey_Details = ResponseEDit.get_entries;
    Set_Survey_Details(Call_Survey_Details)
       

  }, [])

  const handleSubmit = (event) => {
    event.preventDefault();

    setMessage("");
    setSuccessful(false);

    setMessage("");
    setLoading(true);


    form.current.validateAll();

    //get the user from the localstorage
    const mytoken = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
    const token = mytoken.token;
    // const state= mytoken

    //get single survey details
    const ResponseEDit = JSON.parse(localStorage.getItem('ResponseEDit'));

    //get the survey Pk  to send as require survey_pk
    const ResponseEDitPk = ResponseEDit.pk
    const state_pk = ResponseEDit.state
    const lga_pk = ResponseEDit.LGA
    const responseForm=ResponseEDit.get_entries;
    //Save all user responses of the survey  to LocalStorage
    const to_Send = JSON.parse(localStorage.getItem('Response_to_Send'))
    // returns an array of a given object's own enumerable property values
 
    //Response Body to send to Mallam Ahmad
    const Responsebody =
    {
      response_pk: ResponseEDitPk,
      lga_pk: lga_pk,
      state_pk: state_pk,
      form: Object.values(to_Send) || responseForm
    }

    if (checkBtn.current.context._errors.length === 0) {

      // console.log(Responsebody);
      axios.post("https://surveyng.cbn.gov.ng/api/survey/api/edit/response/", Responsebody,
        {
          headers:
          {
            'Content-Type': 'application/json',
            'Authorization': `Token ${token}`,
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
            'Access-Control-Allow-Credentials': true
          },
        })
        .then(res => {

          if (res.data) {
            localStorage.setItem("Response_Approve", JSON.stringify(res.data));
          }
          setMessage(res.data.detail);
          setSuccessful(true);
        },
          (error) => {
            const resMessage =
              (error.response &&
                error.response.data.detail &&
                error.response.data.detail) ||
              error.survey_title ||
              error.toString();

            setMessage(resMessage);
            setSuccessful(false);
          }
        );
      // console.log(Get_Survey_Details)
    }
  }

  //this handle the change user typing
  const handleChange = (id, event) => {

    const newElements = { ...Get_Survey_Details }
    const arr = [...Get_Survey_Details];


    //convert the categories objects to array
    for (let n = 0; n < newElements.length; n++) {
      arr.push(newElements[n]);
    }

    //first array to loop through the categories
    for (let i = 0; i < arr.length; i++) {
      const category = arr[i];

      //second array to loop through the field
      for (let j = 0; j < category.fields.length; j++) {
        const field = category.fields[j];

        //check the pk for each field
        const { pk } = field;
        if (id === pk) {
          field['value'] = event.target.value;
        }
      }

      //first array to loop through the SubCategories
      for (let h = 0; h < category.sub_categories? category.sub_categories.length:0; h++) {
        const SubCategory = category.sub_categories[h];

        //second array to loop through the fields of SubCategory
        for (let j = 0; j < SubCategory.fields? SubCategory.fields.length:0; j++) {
          const FormElementCategory = SubCategory.fields[j];
          // console.log(FormElementCategory)

         
          //check the pk for each field
          const { pk } = FormElementCategory;
          if (id === pk) {
            FormElementCategory['value'] = event.target.value;
          }
        }
      }
    }
    //sub_categories

    //Save the result to the localStorage
    localStorage.setItem("Response_to_Send", JSON.stringify(Get_Survey_Details));
  }

  if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
    return (<Redirect to={'/login'} />)
  }
  // const checkUser= JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
const link="https://gfso.chinikiguard.com"
  return (

    <FormContext.Provider value={{ handleChange }}>
      <Header />
      <div className="content-wrapper" style={{ minHeight: 1602 }}>
        <Menu /><br />
        <section className="content">
          <div className="container-fluid">
            <div className="row">
              {/* <div className="card card-"> */}
              {/* left column */}
              

              <div className="col-md-12">
                <Form onSubmit={handleSubmit} ref={form}>
                  {!successful && (
                    <div>
                      <center><h1 class="mt-4 mb-2">{ResponseEDit.title}</h1></center>
                      <center><p class="mt-4 mb-2"><b>Survey Type: &nbsp;</b>{ResponseEDit.survey_type.toUpperCase()}&nbsp; <b>Period: &nbsp;</b>{ResponseEDit.quarter.toUpperCase()}&nbsp; <b>Year: &nbsp;</b>{ResponseEDit.year}</p></center>
                      <center><p class="mt-4 mb-2"><b>Filled By: &nbsp;</b>{ResponseEDit.filled_by.toUpperCase()}<b>Period: &nbsp;</b>{ResponseEDit.quarter.toUpperCase()}&nbsp; <b>Phone Number: &nbsp;</b>{ResponseEDit.filled_by_phone}</p></center>
                        
                      {/* <CheckSurveyTypeResponse /> */}
                      {Call_Survey_Details.map(myfield => (

                        <div className="card card-">
                           <div className="card-header">
                                            <h3 className="card-title">{myfield.economic_code}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<b>{myfield.name}</b>
                                            </h3>
                                            {/* <h6 className="">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{myfield.type}</h6> */}
                                            <h6 className="" style={{ fontStyle: 'italic' }}>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{myfield.heading}</h6>

                                        </div>
                         
                          <div className="card-body">
                            {/* <h6 className="">{myfield.type}</h6> */}
                            <div className="row">
                              {
                                myfield ? myfield.fields.map((field, i) => <FormElementRes key={i} field={field} />)
                                  : null
                              }
                            </div>
                          </div>
                          {myfield.sub_categories.map(subCategory => (
                            <div>
                              <h6 className="">{subCategory.name}</h6>
                              <div className="col-12">
                                <div className="row">
                                  {
                                    subCategory.fields && subCategory.fields.map((field_cat, i) => <FormElementCatRes  key={i} field_cat={field_cat} />)
                                     
                                  }
                                </div>
                              </div>
                            </div>
                          ))}
                        </div>
                      ))}

                        <div className="card-body">
                        <div className="card-footer bg-white">

                        {ResponseEDit.supporting_documents.length!==0 ?

                          <div className="tab-content">
                            <div className="active tab-pane" id="activity">

                              <div className="card-footer bg-white">
                                <ul className="mailbox-attachments d-flex align-items-stretch clearfix">
                                {ResponseEDit.supporting_documents.map(document => (

                                  <li>
                                    <span className="mailbox-attachment-icon"><i className="far fa-file-pdf" /></span>
                                    <div className="mailbox-attachment-info">
                                      <a href={link + document.document} download="hoto" rel="noreferrer" target="_blank" className="mailbox-attachment-name" ><i className="fas fa-paperclip" /> {document.info}</a>
                                      <span className="mailbox-attachment-size clearfix mt-1">
                                        <span>{document.title}</span>
                                        <a href={link + document.document} target="_blank" rel="noreferrer" className="btn btn-default btn-sm float-right" download="gfso"><i className="fas fa-cloud-download-alt" /></a>
                                      </span>
                                    </div>
                                  </li>
                      ))}

                                </ul>
                              </div>
                            </div>

                          </div>

                       :                <center> <i className="nav-icon fas fa-warning text-warning text-lg"></i><h6 style={{ color: 'red'}}>Sorry there is no any Supporting document attached to this Survey</h6></center>

                      }
                     </div>
                     

                        </div>
                      <div className="form-group">
                        {/* &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; */}
                        <center> <button className="btn btn-success btn-lg " disabled={loading}>
                          {loading && (
                            <span className="spinner-grow spinner-grow-sm"></span>
                          )}
                          <span>Approve</span>
                        </button>
                        </center>
                      </div>
                    </div>
                  )}
                  <h3>
                    <center>
                      {message && (
                        <div className="form-group">
                          <div
                            className={
                              successful ? "alert alert-success" : "alert alert-danger"
                            }
                            role="alert"
                          >
                            {message}
                            <br/>
                            <br/>

                              {successful &&
                                <Link
                                  className="btn btn-primary"
                                  to="/ApproveResponse"

                                >


                                  continue
                               </Link>
                              }
                          </div>
                        </div>
                      )}
                    </center>

                  </h3>

                  <CheckButton style={{ display: "none" }} ref={checkBtn} />
                </Form>
              </div>

            </div>
            {/* </div> */}
          </div>
        </section>
      </div>
      <Footer />
    </FormContext.Provider>
  );
}

export default ResponseEdit;

