import React from 'react'
import TextInput from './FormElements/TextInput'
import TextInputPensionRes from './FormElements/TextInputPensionRes'
import SelectOption from './FormElements/SelectOption'

const FormElement = ({ field: { input_type, pk, name, value,amount,field_value }}) => {
  
    switch (input_type) {
        case 'text_input_':
            return (<TextInput
                pk={pk}
                name={name}
                input_type={input_type}
                value={value}

            />)
            case 'number':
                return (<TextInput
                    pk={pk}
                    name={name}
                    input_type={input_type}
                    value={value}
                    field_value={field_value}
                />)
                case 'pension':
                return (<TextInputPensionRes
                    pk={pk}
                    name={name}
                    input_type={input_type}
                    value={amount}
                    field_value={field_value}
                />)
                
            case 'accounting':
            return (<SelectOption
                pk={pk}
                name={name}
                input_type={input_type}
                value={value}
                field_value={field_value}
            />)
        

        default:
            return null;
    }


}

export default FormElement
