// };
export default class TemplateToApprove extends Component {

    constructor(props) {
        super(props);
        this.onChangeApprove = this.onChangeApprove.bind(this);
        this.onChangeRemark = this.onChangeRemark.bind(this);

        this.state = {
            warningLoad: false,
            myloading: true,
            loading:false,
            message: "",
            Templates: [],
            ApproveThisSurvey:[],
            approve:"",
            remark:""
        };
    }
    
    onChangeApprove(e) {
        this.setState({
            approve: e.target.value
        });
      }
       onChangeRemark(e) {
        this.setState({
            remark: e.target.value
        });
      }
    componentDidMount() {
        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }
        const mytoken = authService.getCurrentUser();
        const token = mytoken.token;
        axios.get("https://surveyng.cbn.gov.ng/api/survey/api/list/survey/?has_template=yes&approved_template=no",
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                if (res.data) {
                    localStorage.setItem("TemplateToApprove", JSON.stringify(res.data));
                }
                // (res);
                // (res.data);
                this.setState({
                    Templates: res.data,
                    myloading: false,
                    warningLoad: true,
                });
            })

    }

  
    ApproveTemplate(survey){

        const surveyId = survey.pk
        // console.log(survey.pk)
        localStorage.setItem("Survey_Update_ID", JSON.stringify(survey.pk));
        const mytoken = authService.getCurrentUser();
        const token = mytoken.token;

        this.setState({
            loadingModal: true,
          });
        axios.get(`https://surveyng.cbn.gov.ng/api/survey/api/details/${surveyId}/`,
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                window.location = "/ApproveTemplate"

                if (res.data) {
                    localStorage.setItem("ApproveTemplateInfo", JSON.stringify(res.data));
                }

                this.setState({
                  ApproveThisSurvey: res.data,
                //   loadingModal: false,
                });

              })
    
      }

    render() {

        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/'} />)
        }


        return (
            <div>
                <Header />
                <Menu />
                <div className="content-wrapper">
                         <br />
                         {this.state.loadingModal && (
                        <span className="">
                            <div className="overlay-wrapper">
                                <div className="overlay ">         
                      <center> <img src={Logo} height="50px" alt="" /></center><br />
                             <div className="text-bold pt-2"> &nbsp;&nbsp;Please wait ...</div></div>
                            </div>

                        </span>
                     )}
                <section className="content">
                              <div className="container-fluid">

                                   <div className="row">

                                        <div className="col-12">
                                             {this.state.myloading ? (
                                                  <>
                                                       <center><Spinner animation="border" variant="success" /></center>
                                                  </>
                                             ) : (

                                                  <div className="row">
                                                       <div className="col-12">
                                                            <div className="card">
                                                                 <div className="card-header">
                                                                      <h3 className="card-title">Templates to Approve</h3>
                                                                      <div className="card-tools">
                                                                           
                                                                      </div>
                                                                 </div>
                                                                 {/* /.card-header */}
                                                                 {this.state.myloading ? (
                                                                 <center><Spinner animation="border" variant="success" /></center>
                                                                  ) : (
                                                                 <div className="card-body table-responsive p-0">
                                                                      <table className="table table-hover text-nowrap">
                                                                           <thead>
                                                                                <tr>
                                                                                     <th>Title</th>
                                                                                     <th>Type</th>
                                                                                     <th>Year</th>
                                                                                     <th>Quarter</th>
                                                                                     <th>Created by</th>
                                                                                     <th>Action </th>
                                                                                </tr>
                                                                           </thead>
                                                                           {this.state.Templates.map(survey => (

                                                                           <tbody>
                                                                                <tr>
                                                                                     <td>{survey.title}</td>
                                                                                     <td>{survey.survey_type}</td>
                                                                                     <td>{survey.year}</td>
                                                                                     <td>{survey.quarter}</td>
                                                                                     <td><span className="tag tag-success">{survey.created_by}</span></td>
                                                                                     <td className="">
                                                                                           <button
                                                                                               className="btn btn-primary"
                                                                                               disabled={this.state.loading}
                                                                                               onClick={this.ApproveTemplate.bind(this, survey)}

                                                                                           >View
                                                                                         </button>
                                                                                           
                                                                                         
                                                                                         </td>
                                             

                                                                                </tr>
                                                                               
                                                                           </tbody>
                                                                           ))}
                                                                      </table>
                                                                 </div>
                                                                )}
                                                                 {/* /.card-body */}
                                                            </div>
                                                            {/* /.card */}
                                                       </div>
                                                  </div>

                                             )}
                                        </div>


                                   </div>


                              </div>


                         </section>

                </div>
                <Footer />

                </div>
        );
    }
}

             