import React from 'react'
import InputRes from './FormElements/InputRes'
import InputResAccounting from './FormElements/InputResAccounting'
// import SelectOption from './FormElements/SelectOption'

const FormElementCatRes = ({ field_cat: { input_type, pk, name, value }}) => {
  
    switch (input_type) {
        case 'number':
            return (<InputRes
                pk={pk}
                name={name}
                input_type={input_type}
                value={value}

            />)
        case 'text_input_':
            return (<InputResAccounting
                pk={pk}
                name={name}
                input_type={input_type}
                value={value}
            />)
       

        default:
            return null;
    }


}

export default FormElementCatRes
