import 'react-form-builder2/dist/app.css';
import React, { Component } from "react";
import { Redirect } from 'react-router-dom';
import Header from './Header';
import Footer from './Footer'
import axios from "axios";
import Menu from "./Menu";
import { ReactFormBuilder } from 'react-form-builder2';
import Demobar from "./Demobar";
import * as variables from './variables';


export default class CategoryView extends Component {

    constructor(props) {
        super(props);
        this.handleSubmit = this.handleSubmit.bind(this);
     

        this.state = {
            loading: false,
            message: "",

        };
    }


    handleSubmit(e) {
        e.preventDefault();

        this.setState({
            message: "",
            successful: false,
            loading: true,
            meloading: true

        });

        const mytoken = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
        const token = mytoken.token;

        const responses = JSON.parse(localStorage.getItem('SingleResponse'));
        const response_data = responses.data;
        const get_form = response_data.get_form;
        // console.log(response_data);
        const value = this.state.title;


        

        const body =
        {
            survey_response_pk: response_data.pk,
            form:

                [
                    {
                        get_form,
                        value: value
                    }

                ]


        }

        console.log(body);

        axios.post("https://surveyng.cbn.gov.ng/api/survey/api/add/response/entry/", body,
            {
                headers:
                {
                    'Content-Type': 'application/json',
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                if (res.data) {
                    localStorage.setItem("SuccessSurvey", JSON.stringify(res.data));
                }

                this.setState({
                    message: res.data.detail,
                    successful: true,
                    loading: false,
                    meloading: false
                });

                console.log(res);
                console.log(res.data.detail);
                // window.location = "/dashboard"
            },

                error => {
                    const resMessage =
                        (error.response &&
                            error.response.data.detail &&
                            error.response.data.detail) ||
                        error.message ||
                        error.toString();

                    this.setState({
                        successful: false,
                        message: resMessage,
                        loading: false,
                        meloading: false
                    });
                })
    }

    componentDidMount() {
        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }
    }

    render() {

        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/'} />)
        }

        const singleCategory = JSON.parse(localStorage.getItem('Category_to_AddField'));


        return (
            <div>
                <Header />
                <Menu />
                <section className="content-wrapper" >
                    <br />
                    <div className="container-fluid">
                        <div className="row">
                            {/* left column */}
                            <div className="col-md-6">

                                {/* general form elements disabled */}
                                <div className="card card">
                                    <div className="card-header">
                                        <h3 className="card-title">{singleCategory.name}</h3>
                                    </div>
                                    {/* /.card-header */}

                                    <div className="card-body">
                                        <br />

                                        <form>
                                            <div className="row">
                                                {singleCategory.fields.map(fields => (

                                                    <div className="col-4">
                                                        <input type="text" className="form-control is-valid" placeholder={fields.name} disabled id="inputSuccess" />                                                <br />
                                                    </div>
                                                ))}

                                            </div>
                                            {singleCategory.sub_categories.map(subCategory => (
                                                    <div>
                                                        <h6 className="">{subCategory.name}</h6>
                                                        <div className="row">
                                                            {subCategory.fields.map(SubCategoryfields => (
                                                                <div className="col-4">
                                                                    <input type="text" className="form-control is-valid" placeholder={SubCategoryfields.name} disabled  />                                                <br />
                                                                </div>
                                                            ))}
                                                        </div>
                                                    </div>
                                                ))}
                                            {/* input states */}
                                        </form>
                                        <br />


                                    </div>

                                    {/* /.card-body */}
                                </div>
                                {/* /.card */}
                                

                            </div>
                            {/*/.col (left) */}


                            {/* right column */}
                            <div className="col-md-6">

                                {/* general form elements disabled */}
                                <div className="card card">
                                    <div className="card-header">
                                        <h3 className="card-title"> </h3>
                                    </div>
                                    {/* /.card-header */}

                                    <div className="card-body">
                                        <br />

                                            <ReactFormBuilder />                
                                            <Demobar variables={variables} />



                                            {/* input states */}
                                        <br />


                                    </div>

                                    {/* /.card-body */}
                                </div>
                                {/* /.card */}
                            <br />
                           




                            </div>
                            {/*/.col (right) */}
                        </div>
                    </div>
                </section>
                <Footer />
            </div>

        );
    }
}