import React, { Component } from 'react';
import {   Link, Switch } from "react-router-dom";
// import authService from '../services/auth.service';
import axios from 'axios'

class Header extends Component {


  constructor(props) {
    super(props);
    this.routeChange = this.routeChange.bind(this);
    this.logOut = this.logOut.bind(this);


  this.state = {
      message: []
  };
  }
  // state = {
  //   navigate: false
  // };
  componentDidMount() {


    const user = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
    const token = user.token;
    //call the States and LGAS at the first 
    axios.get('https://surveyng.cbn.gov.ng/api/core/api/user/notification/list/?read=no',
      {
        headers:
        {
          'Content-Type': 'application/json',
          'Authorization': `Token ${token}`,
        },

      },
    )
      .then(res => {
        if (res.data) {
          localStorage.setItem("notification", JSON.stringify(res.data));
        }
        this.setState({
          notification: res.data.length,
          message: res.data
        });
        // console.log(res.data)                  

      })

      
  }
  logOut() {
   localStorage.clear();
    window.location = "/"
    //  (<Redirect to="/login" />)
  }
  routeChange = () => {
    this.setState({ navigate: true });
    window.location = "/dashboard"
  };
  

  render() {

    // const { navigate } = this.state;

    return (

      <Switch>
        {/* {currentUser && ( */}

        <nav className="main-header navbar navbar-expand navbar-white navbar-light">
          {/* Left navbar links */}
          <ul className="navbar-nav">
            <li className="nav-item">
              <Link className="nav-link" data-widget="pushmenu" to="#" role="button"><i className="fas fa-bars" /></Link>
            </li>
            {/* <li className="nav-item d-none d-sm-inline-block">
      <a href="../../index3.html" className="nav-link">Home</a>
    </li> */}
            {/* <li className="nav-item d-none d-sm-inline-block">
      <a href="#" className="nav-link">Menu</a>
    </li> */}
          </ul>
          {/* Right navbar links */}
          <ul className="navbar-nav ml-auto">
            {/* Navbar Search */}

            {/* Messages Dropdown Menu */}
            <li className="nav-item">
              <Link  to="/Notifications" className="nav-link"  >
                <i className="fas fa-bell" />
                <span className="badge badge-danger navbar-badge">{this.state.notification}</span>
              </Link>
             
            </li>


            <li className="nav-item">
              <Link className="nav-link" data-widget="fullscreen" to="#" role="button">
                <i className="fas fa-expand-arrows-alt" />
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" data-slide="true" data-widget="control-sidebar" onClick={this.logOut}  >
                <span>Sign Out</span> &nbsp;
        <i className="fas fa-user-alt" />
              </Link>
            </li>
          </ul>
        </nav>

      </Switch>

    )
  }

}


export default Header;



